import React, { useState, useEffect, useRef } from "react";

// @material-ui/core components
import withStyles from "@material-ui/core/styles/withStyles";

// material-ui icons
import Assignment from "@material-ui/icons/Assignment";
import PanToolIcon from "@material-ui/icons/Check";
import PaymentIcon from "@material-ui/icons/Payment";
import Close from "@material-ui/icons/Close";

// core components
import GridContainer from "../../components/Grid/GridContainer.jsx";
import GridItem from "../../components/Grid/GridItem.jsx";
import Table from "../../components/Table/Table.jsx";
import Button from "../../components/CustomButtons/Button.jsx";
import Card from "../../components/Card/Card.jsx";
import CardBody from "../../components/Card/CardBody.jsx";
import CardIcon from "../../components/Card/CardIcon.jsx";
import CardHeader from "../../components/Card/CardHeader.jsx";

import extendedTablesStyle from "../../assets/jss/material-dashboard-pro-react/views/extendedTablesStyle.jsx";
import axios from "axios";
import {
  TextField,
  Tooltip,
  Dialog,
  DialogTitle,
  Slide,
  DialogActions,
  DialogContent,
} from "@material-ui/core";
import { debounce } from "../../functions.js";
import { toast } from "react-toastify";

let host = process.env.REACT_APP_API_URL;

function Transition(props) {
  return <Slide direction="down" {...props} />;
}

function AcceptedOrdersd(props) {
  const [modal, setmodal] = useState({
    classicModal: false,
  });

  let textInput = useRef(null);
  const [state, setstate] = useState({
    order_name: "",
    delivery_date: "",
  });

  const [orders, setorders] = useState([]);
  const [myOrder, setmyOrder] = useState(null);

  const handleClickOpen = async (modal, order_id) => {
    try {
      let res = await axios.get(`${host}/api/admin/order/${order_id}`);
      if (res) {
        setmyOrder(res.data.order);
      }
    } catch (error) {}

    var x = [];
    x[modal] = true;
    setmodal(x);
  };

  const handleClose = (modal) => {
    var x = [];
    x[modal] = false;
    setmodal(x);
  };

  const handleChange = (e) => {
    setstate({ ...state, [e.target.name]: e.target.value });

    if (e.target.value == "") {
      fetchAllOrders();
    } else {
      fetchOrders(e.target.value);
      textInput.current.value = "";
    }
  };

  const fetchOrders = debounce(async (e) => {
    let res = await axios.get(
      `${host}/api/admin/order?order_name=${state.order_name}&order_status_id=7`
    );

    if (state.order_name) {
      res = await axios.get(
        `${host}/api/admin/order?order_name=${state.order_name}&order_status_id=7`
      );
    } else {
      res = await axios.get(
        `${host}/api/admin/order?delivery_date=${new Date().toLocaleDateString(
          "en-CA"
        )}&order_status_id=7`
      );
    }

    let data = [];
    if (res) {
      for (let i = 0; i < res.data.orders.rows.length; i++) {
        const el = res.data.orders.rows[i];

        data.push([
          roundButtons(el),
          <a onClick={() => handleClickOpen("classicModal", el["id"])}>
            {el["order_name"]}
          </a>,
          el["payment_method"],
          el["payment_status"]["name"],
          el["order_status"]["name"],
          el["total"],
          // `${el["delivery_date"]} \n ${el["delivery_time"]}`,
          `${el["order_detail"]["shipping_address"]}, ${el["order_detail"]["shipping_city"]}, ${el["order_detail"]["shipping_province"]} , ${el["order_detail"]["shipping_zip"]}`,
          el["created_at"],
        ]);
      }

      setorders(data);
    }
  }, 250);

  const fetchOrdersByDate = async (e) => {
    let res = await axios.get(
      `${host}/api/admin/order?delivery_date=${e.target.value}`
    );
    let data = [];
    if (res) {
      for (let i = 0; i < res.data.orders.rows.length; i++) {
        const el = res.data.orders.rows[i];

        data.push([
          roundButtons(el),
          <a onClick={() => handleClickOpen("classicModal", el["id"])}>
            {el["order_name"]}
          </a>,
          el["payment_method"],
          el["payment_status"]["name"],
          el["order_status"]["name"],
          el["total"],
          // `${el["delivery_date"]} \n ${el["delivery_time"]}`,
          `${el["order_detail"]["shipping_address"]}, ${el["order_detail"]["shipping_city"]}, ${el["order_detail"]["shipping_province"]} , ${el["order_detail"]["shipping_zip"]}`,
          el["created_at"],
        ]);
      }

      setorders(data);
    }
  };

  const fetchAllOrders = async () => {
    let res = await axios.get(`${host}/api/admin/order?order_status_id=7`);
    let data = [];
    if (res) {
      for (let i = 0; i < res.data.orders.rows.length; i++) {
        const el = res.data.orders.rows[i];

        data.push([
          roundButtons(el),
          <a onClick={() => handleClickOpen("classicModal", el["id"])}>
            {el["order_name"]}
          </a>,
          el["payment_method"],
          el["payment_status"]["name"],
          el["order_status"]["name"],
          el["total"],
          // `${el["delivery_date"]} \n ${el["delivery_time"]}`,
          `${el["order_detail"]["shipping_address"]}, ${el["order_detail"]["shipping_city"]}, ${el["order_detail"]["shipping_province"]} , ${el["order_detail"]["shipping_zip"]}`,
          el["created_at"],
        ]);
      }

      setorders(data);
    }
  };

  const handleOrder = async (label, order) => {
    if (window.confirm(`Are you sure you want to ${label} this order?`)) {
      try {
        if (label == "cancel") {
          let res = await axios.patch(
            `${host}/api/admin/order/${order["id"]}`,
            {
              delivery_date: order["delivery_date"],
              delivery_time: order["delivery_time"],
              payment_status_id: order["payment_status_id"],
              payment_method: order["payment_method"],
              order_status_id: 9,
            }
          );

          if (res) {
            setorders((prevState) => {
              let newOrders = prevState.map((el) =>
                el[1] == order["order_name"]
                  ? [...el, (el[4] = "canceled")]
                  : el
              );

              return [...prevState];
            });
            toast.success("Order updated successfully");
          }
        } else if (label == "accept") {
          let res = await axios.patch(
            `${host}/api/admin/order/${order["id"]}`,
            {
              delivery_date: order["delivery_date"],
              delivery_time: order["delivery_time"],
              payment_status_id: order["payment_status_id"],
              payment_method: order["payment_method"],
              order_status_id: 2,
            }
          );

          if (res) {
            setorders((prevState) => {
              let newOrders = prevState.map((el) =>
                el[1] == order["order_name"]
                  ? [...el, (el[4] = "accepted")]
                  : el
              );

              console.log(newOrders, prevState);
              return [...prevState];
            });
            toast.success("Order updated successfully");
          }
        } else if (label == "paid") {
          let res = await axios.patch(
            `${host}/api/admin/order/${order["id"]}`,
            {
              delivery_date: order["delivery_date"],
              delivery_time: order["delivery_time"],
              payment_status_id: 2,
              payment_method: order["payment_method"],
              order_status_id: order["order_status_id"],
            }
          );

          if (res) {
            setorders((prevState) => {
              let newOrders = prevState.map((el) =>
                el[1] == order["order_name"] ? [...el, (el[3] = "paid")] : el
              );

              console.log(newOrders, prevState);
              return [...prevState];
            });
            toast.success("Order updated successfully");
          }
        }
      } catch (error) {}
    }
  };

  useEffect(() => {
    fetchAllOrders();
  }, []);

  const { classes } = props;
  const fillButtons = [
    { color: "info", icon: PanToolIcon },
    { color: "danger", icon: Close },
  ].map((prop, key) => {
    return (
      <Button color={prop.color} className={classes.actionButton} key={key}>
        <prop.icon className={classes.icon} />
      </Button>
    );
  });
  const simpleButtons = [
    { color: "info", icon: PanToolIcon },
    { color: "danger", icon: Close },
  ].map((prop, key) => {
    return (
      <Button
        color={prop.color}
        simple
        className={classes.actionButton}
        key={key}
      >
        <prop.icon className={classes.icon} />
      </Button>
    );
  });
  const roundButtons = (order_id) =>
    [
      // { color: "info", icon: PanToolIcon, label: "accept" },
      // { color: "info", icon: PaymentIcon, label: "paid" },
      { color: "danger", icon: Close, label: "cancel" },
    ].map((prop, key) => {
      return (
        <Tooltip title={prop.label}>
          <Button
            round
            color={prop.color}
            onClick={() => handleOrder(prop.label, order_id)}
            className={classes.actionButton + " " + classes.actionButtonRound}
            key={key}
          >
            <prop.icon className={classes.icon} />
          </Button>
        </Tooltip>
      );
    });
  return (
    <GridContainer>
      <GridItem xs={12}>
        <Card>
          <CardHeader color="rose" icon>
            <CardIcon color="rose">
              <Assignment />
            </CardIcon>
            <h4 className={classes.cardIconTitle}>Orders</h4>
            <br />
            <GridContainer>
              <GridItem xs={12} sm={12} md={2}>
                <TextField
                  id="standard-basic"
                  label="Filter Orders"
                  name="order_name"
                  onChange={(e) => handleChange(e)}
                />
              </GridItem>
              <GridItem xs={12} sm={12} md={2}>
                <TextField
                  id="date"
                  label="Delivery Date"
                  type="date"
                  inputRef={textInput}
                  name="delivery_date"
                  // defaultValue={new Date().toLocaleDateString("en-CA")}
                  onChange={fetchOrdersByDate}
                  className={classes.textField}
                  InputLabelProps={{
                    shrink: true,
                  }}
                />
              </GridItem>
              {/* <GridItem xs={12} sm={12} md={2}>
                <FormControl className={classes.select_input}>
                  <InputLabel id="demo-simple-select-label">Age</InputLabel>
                  <Select
                    labelId="demo-simple-select-label"
                    id="demo-simple-select"
                  >
                    <MenuItem value={10}>Ten</MenuItem>
                    <MenuItem value={20}>Twenty</MenuItem>
                    <MenuItem value={30}>Thirty</MenuItem>
                  </Select>
                </FormControl>
              </GridItem> */}
            </GridContainer>
          </CardHeader>
          <CardBody>
            <Table
              tableHead={[
                "Actions",
                "Order Name",
                "Payment",
                "Payment Status",
                "Order Status",
                "Amount",
                // "Delivery",
                "Address",
                "Created",
              ]}
              tableData={orders}
              customCellClasses={[classes.center, classes.right, classes.right]}
              customClassesForCells={[0, 4, 5]}
              customHeadCellClasses={[
                classes.center,
                classes.right,
                classes.right,
              ]}
              customHeadClassesForCells={[0, 4, 5]}
            />
          </CardBody>
        </Card>
      </GridItem>

      <Dialog
        fullWidth={true}
        maxWidth="lg"
        classes={{
          root: classes.center + " " + classes.modalRoot,
          paper: classes.modal,
          scrollPaper: classes.scrollPaper,
        }}
        open={modal.classicModal}
        TransitionComponent={Transition}
        keepMounted
        onClose={() => handleClose("classicModal")}
        aria-labelledby="classic-modal-slide-title"
        aria-describedby="classic-modal-slide-description"
      >
        <DialogTitle
          id="classic-modal-slide-title"
          disableTypography
          className={classes.modalHeader}
        >
          <Button
            justIcon
            className={classes.modalCloseButton}
            key="close"
            aria-label="Close"
            color="transparent"
            onClick={() => handleClose("classicModal")}
          >
            <Close className={classes.modalClose} />
          </Button>
        </DialogTitle>
        <DialogContent>
          {myOrder ? (
            <>
              <h3>{myOrder["order_name"]}</h3>
              <hr />
              <h5>Shipping</h5>
              <h5>
                {myOrder["order_detail"]["shipping_first_name"]}{" "}
                {myOrder["order_detail"]["shipping_last_name"]}
              </h5>
              <h5>{myOrder["order_detail"]["shipping_phone"]}</h5>
              <h5>
                {myOrder["order_detail"]["shipping_address"]}{" "}
                {myOrder["order_detail"]["shipping_city"]}{" "}
                {myOrder["order_detail"]["shipping_province"]}{" "}
                {myOrder["order_detail"]["shipping_zip"]}
              </h5>
              <hr />
              <h5>Billing</h5>
              <h5>
                {myOrder["order_detail"]["billing_first_name"]}{" "}
                {myOrder["order_detail"]["billing_last_name"]}
              </h5>
              <h5>{myOrder["order_detail"]["billing_phone"]}</h5>
              <h5>{myOrder["order_detail"]["billing_email"]}</h5>
              <hr />
              <h5>Order Items</h5>
              {myOrder["order_items"].map((item, i) => {
                return (
                  <>
                    <h5>
                      {item["product"]["name"]} x{item["quantity"]}
                    </h5>
                    <h5>{item["item_price"]}</h5>
                  </>
                );
              })}
              <h5>Total: {myOrder["total"]}</h5>
            </>
          ) : (
            <h4>Loading, please wait...</h4>
          )}
        </DialogContent>

        <DialogActions className={classes.modalFooter}>
          <Button
            onClick={() => handleClose("classicModal")}
            color="danger"
            simple
          >
            Close
          </Button>
        </DialogActions>
      </Dialog>
    </GridContainer>
  );
}

export default withStyles(extendedTablesStyle)(AcceptedOrdersd);
