import React, { useState, useEffect, useRef } from "react";

// @material-ui/core components
import withStyles from "@material-ui/core/styles/withStyles";

// material-ui icons
import Assignment from "@material-ui/icons/Assignment";
import PanToolIcon from "@material-ui/icons/Check";
import PaymentIcon from "@material-ui/icons/Payment";
import Close from "@material-ui/icons/Close";

// core components
import GridContainer from "../Grid/GridContainer.jsx";
import GridItem from "../Grid/GridItem.jsx";
import Table from "../Table/Table.jsx";
import Button from "../CustomButtons/Button.jsx";
import Card from "../Card/Card.jsx";
import CardBody from "../Card/CardBody.jsx";
import CardIcon from "../Card/CardIcon.jsx";
import CardHeader from "../Card/CardHeader.jsx";

import extendedTablesStyle from "../../assets/jss/material-dashboard-pro-react/views/extendedTablesStyle.jsx";
import axios from "axios";
import {
  TextField,
  Tooltip,
  Dialog,
  DialogTitle,
  Slide,
  DialogActions,
  DialogContent,
  Select,
  MenuItem,
  FormControl,
  InputLabel,
} from "@material-ui/core";
import { toast } from "react-toastify";

let host = process.env.REACT_APP_API_URL;

function Transition(props) {
  return <Slide direction="down" {...props} />;
}

function Employees(props) {
  const [modal, setmodal] = useState({
    classicModal: false,
  });

  let textInput = useRef(null);
  const [state, setstate] = useState({
    user: "",
    name: "",
    email: "",
    password: "",
    confirm_password: "",
    type: "",
  });

  const [orders, setorders] = useState([]);
  const [isLoading, setisLoading] = useState(false);

  const isValid = () => {
    if (!state.name || !state.user || !state.password) {
      toast.error("Please input all fields");
      return false;
    }

    if (state.password != state.confirm_password) {
      toast.error("Password does not match, Please try again.");
      return false;
    }

    return true;
  };

  const handleClickOpen = async (modal, order_id) => {
    var x = [];
    x[modal] = true;
    setmodal(x);
  };

  const handleClose = (modal) => {
    var x = [];
    x[modal] = false;
    setmodal(x);
  };

  const handleChange = (e) => {
    setstate({ ...state, [e.target.name]: e.target.value });
  };

  const handleSubmit = async () => {
    if (isValid()) {
      setisLoading(true);
      try {
        let res = await axios.post(`${host}/api/admin/user/create`, {
          // product data
          name: state.name,
          user: state.user,
          password: state.password,
          email: state.email,
          type: state.type,
        });
        if (res) {
          fetchAllUsers();
          setmodal(false);
          setisLoading(false);
          toast.success("Employee created successfully");
        }
      } catch (error) {
        setisLoading(false);
        //   console.log(error);
      }
    }
  };

  const fetchAllUsers = async () => {
    let res = await axios.get(`${host}/api/admin/user`);
    let data = [];
    if (res) {
      for (let i = 0; i < res.data.orders.rows.length; i++) {
        const el = res.data.orders.rows[i];

        data.push([
          roundButtons(el),
          el["name"],
          el["email"],
          // el["employee_role_id"],
          "Employee",
          el["created_at"],
        ]);
      }

      setorders(data);
    }
  };

  const handleUser = async (label, order) => {
    if (window.confirm(`Are you sure you want to ${label} this record?`)) {
      try {
        if (label == "delete") {
          let res = await axios.delete(`${host}/api/admin/user/${order["id"]}`);

          if (res) {
            fetchAllUsers();
            toast.success("Record deleted successfully");
          } else {
            toast.error("Something went wrong, Please try again.");
          }
        }
      } catch (error) {}
    }
  };

  useEffect(() => {
    fetchAllUsers();
  }, []);

  const { classes } = props;
  const fillButtons = [
    { color: "info", icon: PanToolIcon },
    { color: "danger", icon: Close },
  ].map((prop, key) => {
    return (
      <Button color={prop.color} className={classes.actionButton} key={key}>
        <prop.icon className={classes.icon} />
      </Button>
    );
  });
  const simpleButtons = [
    { color: "info", icon: PanToolIcon },
    { color: "danger", icon: Close },
  ].map((prop, key) => {
    return (
      <Button
        color={prop.color}
        simple
        className={classes.actionButton}
        key={key}
      >
        <prop.icon className={classes.icon} />
      </Button>
    );
  });
  const roundButtons = (order_id) =>
    [{ color: "danger", icon: Close, label: "delete" }].map((prop, key) => {
      return (
        <Tooltip title={prop.label}>
          <Button
            round
            color={prop.color}
            onClick={() => handleUser(prop.label, order_id)}
            className={classes.actionButton + " " + classes.actionButtonRound}
            key={key}
          >
            <prop.icon className={classes.icon} />
          </Button>
        </Tooltip>
      );
    });
  return (
    <GridContainer>
      <GridItem xs={12}>
        <Card>
          <CardHeader color="rose" icon>
            <CardIcon color="rose">
              <Assignment />
            </CardIcon>
            <h4 className={classes.cardIconTitle}>Orders</h4>
            <br />
            <GridContainer>
              <Button
                onClick={() => handleClickOpen("classicModal")}
                style={{ marginLeft: "10px" }}
              >
                Create Employee
              </Button>
            </GridContainer>
          </CardHeader>
          <CardBody>
            <Table
              tableHead={["Actions", "Name", "Email", "Role", "Created"]}
              tableData={orders}
              customCellClasses={[classes.center, classes.right, classes.right]}
              customClassesForCells={[4, 4, 4, 4, 4, 4, 4, 0, 4]}
              customHeadCellClasses={[
                classes.center,
                classes.right,
                classes.right,
              ]}
              customHeadClassesForCells={[4, 4, 4, 4, 4, 4, 4, 0, 4]}
            />
          </CardBody>
        </Card>
      </GridItem>

      <Dialog
        fullWidth
        maxWidth="sm"
        open={modal.classicModal}
        TransitionComponent={Transition}
        keepMounted
        onClose={() => handleClose("classicModal")}
        aria-labelledby="classic-modal-slide-title"
        aria-describedby="classic-modal-slide-description"
      >
        <DialogTitle
          id="classic-modal-slide-title"
          disableTypography
          className={classes.modalHeader}
        >
          Create Employee
        </DialogTitle>
        <DialogContent>
          <TextField
            autoFocus
            margin="dense"
            id="name"
            label="Username"
            name="user"
            type="text"
            fullWidth
            variant="standard"
            onChange={handleChange}
          />
          <TextField
            autoFocus
            margin="dense"
            id="name"
            label="Full Name"
            name="name"
            type="text"
            fullWidth
            variant="standard"
            onChange={handleChange}
          />
          <TextField
            autoFocus
            margin="dense"
            id="name"
            label="Email"
            name="email"
            type="email"
            fullWidth
            variant="standard"
            onChange={handleChange}
          />

          <FormControl fullWidth className={classes.selectFormControl}>
            <InputLabel htmlFor="simple-select" className={classes.selectLabel}>
              Choose Type
            </InputLabel>
            <Select
              MenuProps={{
                className: classes.selectMenu,
              }}
              classes={{
                select: classes.select,
              }}
              onChange={handleChange}
              inputProps={{
                name: "type",
                id: "simple-select",
                placeholder: "Choose Role",
              }}
              value={state.type}
              fullWidth
            >
              <MenuItem
                disabled
                classes={{
                  root: classes.selectMenuItem,
                }}
              >
                Choose Role
              </MenuItem>
              <MenuItem
                classes={{
                  root: classes.selectMenuItem,
                  selected: classes.selectMenuItemSelected,
                }}
                value="admin"
              >
                Admin
              </MenuItem>
              <MenuItem
                classes={{
                  root: classes.selectMenuItem,
                  selected: classes.selectMenuItemSelected,
                }}
                value="rider"
              >
                Rider
              </MenuItem>
            </Select>
          </FormControl>

          <TextField
            autoFocus
            margin="dense"
            id="name"
            label="Password"
            type="password"
            name="password"
            fullWidth
            variant="standard"
            onChange={handleChange}
          />
          <TextField
            autoFocus
            margin="dense"
            id="name"
            label="Confirm Password"
            type="password"
            name="confirm_password"
            fullWidth
            variant="standard"
            onChange={handleChange}
          />
        </DialogContent>

        <DialogActions className={classes.modalFooter}>
          <Button
            // onClick={() => handleClose("classicModal")}
            disabled={isLoading}
            color="success"
            simple
            onClick={handleSubmit}
          >
            Submit
          </Button>
          <Button
            onClick={() => handleClose("classicModal")}
            disabled={isLoading}
            color="danger"
            simple
          >
            Close
          </Button>
        </DialogActions>
      </Dialog>
    </GridContainer>
  );
}

export default withStyles(extendedTablesStyle)(Employees);
