import React, { useState, useEffect } from "react";
import { NavLink } from "react-router-dom";
// react component for creating dynamic tables
import ReactTable from "react-table";
import Edit from "@material-ui/icons/Edit";

// @material-ui/core components
import withStyles from "@material-ui/core/styles/withStyles";
import Slide from "@material-ui/core/Slide";
import Dialog from "@material-ui/core/Dialog";
import DialogTitle from "@material-ui/core/DialogTitle";
import DialogContent from "@material-ui/core/DialogContent";
import DialogActions from "@material-ui/core/DialogActions";

// @material-ui/icons
import Assignment from "@material-ui/icons/Assignment";
import Close from "@material-ui/icons/Close";
// core components
import GridContainer from "../../components/Grid/GridContainer.jsx";
import GridItem from "../../components/Grid/GridItem.jsx";
import Button from "../../components/CustomButtons/Button.jsx";
import Card from "../../components/Card/Card.jsx";
import CardBody from "../../components/Card/CardBody.jsx";
import CardIcon from "../../components/Card/CardIcon.jsx";
import CardHeader from "../../components/Card/CardHeader.jsx";
import { cardTitle } from "../../assets/jss/material-dashboard-pro-react.jsx";
import { Switch } from "@material-ui/core";
import axios from "axios";
import ProductImage from "../ProductImage/ProductImage";
import CreateCategory from "./CreateCategory";
import EditCategory from "./EditCategory";
import { toast } from "react-toastify";

const styles = {
  cardIconTitle: {
    ...cardTitle,
    marginTop: "15px",
    marginBottom: "0px",
  },
};

let host = process.env.REACT_APP_API_URL;

function Transition(props) {
  return <Slide direction="down" {...props} />;
}

function Products(props) {
  const { classes } = props;

  const [modal, setmodal] = useState({
    classicModal: false,
  });

  const [willDo, setwillDo] = useState(false);
  const [selectedCat, setselectedCat] = useState(null);

  const [state, setstate] = useState({
    data: null,
  });

  // useEffect(() => {
  //   setselectedCat()
  // }, [])

  const handleClickOpen = (modal) => {
    var x = [];
    x[modal] = true;
    setmodal(x);
  };

  const handleClose = (modal) => {
    var x = [];
    x[modal] = false;
    setmodal(x);
    fetchCategories();
  };

  const handleToggle = async (id) => {
    try {
      let res = await axios.patch(`${host}/api/admin/category/status/${id}`);
      if (res) {
        toast.success("Category updated successfully!");
        fetchCategories();
      }
    } catch (error) {
      toast.error("Something went wrong, Please try again.");
      console.log(error);
    }
  };

  const deleteCategory = async (id) => {
    if (window.confirm("Are you sure you want to delete this category?")) {
      try {
        let res = await axios.delete(`${host}/api/admin/category/${id}`);
        if (res) {
          fetchCategories();
        }
      } catch (error) {
        toast.error("Something went wrong, Please try again.");
        console.log(error);
      }
    }
  };

  const fetchCategories = async () => {
    try {
      try {
        let res = await axios.get(`${host}/api/admin/category`);

        if (res) {
          // setproducts();
          setstate({
            ...state,
            data: res.data.categories.map((prop, key) => {
              return {
                id: prop.id,
                name: prop.name,
                published: (
                  <Switch
                    defaultChecked={prop.status ? true : false}
                    onChange={() => handleToggle(prop.id)}
                    color="primary"
                    name="checkedB"
                    inputProps={{ "aria-label": "primary checkbox" }}
                  />
                ),
                actions: (
                  // we've added some custom button actions
                  <div className="actions-right">
                    {/* use this button to add a like kind of action */}
                    <Button
                      justIcon
                      round
                      simple
                      onClick={() => {
                        setselectedCat(prop);
                        setwillDo("edit");
                        handleClickOpen("classicModal");
                      }}
                      color="warning"
                      className="edit"
                    >
                      <Edit />
                    </Button>
                    {/* use this button to remove the data row */}
                    <Button
                      justIcon
                      round
                      simple
                      onClick={() => {
                        deleteCategory(prop.id);
                      }}
                      color="danger"
                      className="remove"
                    >
                      <Close />
                    </Button>{" "}
                  </div>
                ),
              };
            }),
          });
        }
      } catch (error) {
        console.log(error);
      }
    } catch (error) {}
  };

  useEffect(() => {
    fetchCategories();
  }, []);

  return (
    <GridContainer>
      <GridItem xs={12}>
        <Card>
          <CardHeader color="primary" icon>
            <CardIcon color="primary">
              <Assignment />
            </CardIcon>
            <div style={{ display: "flex", justifyContent: "space-between" }}>
              <h4 className={classes.cardIconTitle}>Categories</h4>
              <div style={{ marginTop: "0.5rem" }}>
                <Button
                  onClick={() => {
                    setwillDo("add");
                    handleClickOpen("classicModal");
                  }}
                >
                  Add New Category
                </Button>
              </div>
            </div>
          </CardHeader>
          <CardBody>
            {state.data ? (
              <ReactTable
                data={state.data}
                filterable
                columns={[
                  {
                    Header: "ID",
                    accessor: "id",
                    maxWidth: "100",
                  },
                  {
                    Header: "Category",
                    accessor: "name",
                  },
                  {
                    Header: "Published",
                    accessor: "published",
                    maxWidth: "100",
                  },
                  {
                    Header: "Actions",
                    accessor: "actions",
                    sortable: false,
                    filterable: false,
                  },
                ]}
                defaultPageSize={10}
                showPaginationTop
                showPaginationBottom={false}
                className="-striped -highlight"
              />
            ) : <h2>Loading, please wait...</h2>}
          </CardBody>
        </Card>
      </GridItem>

      <Dialog
        maxWidth="lg"
        classes={{
          root: classes.center + " " + classes.modalRoot,
          paper: classes.modal,
        }}
        open={modal.classicModal}
        TransitionComponent={Transition}
        keepMounted
        onClose={() => handleClose("classicModal")}
        aria-labelledby="classic-modal-slide-title"
        aria-describedby="classic-modal-slide-description"
      >
        <DialogTitle
          id="classic-modal-slide-title"
          disableTypography
          className={classes.modalHeader}
        >
          <Button
            justIcon
            className={classes.modalCloseButton}
            key="close"
            aria-label="Close"
            color="transparent"
            onClick={() => handleClose("classicModal")}
          >
            <Close className={classes.modalClose} />
          </Button>
          {/* <h4 className={classes.modalTitle}>{product.name}</h4> */}
        </DialogTitle>
        <DialogContent
          id="classic-modal-slide-description"
          className={classes.modalBody}
        />
        {willDo == "add" && <CreateCategory handleClose={handleClose} />}
        {willDo == "edit" && <EditCategory category={selectedCat} />}
        <DialogActions className={classes.modalFooter}>
          <Button
            onClick={() => handleClose("classicModal")}
            color="danger"
            simple
          >
            Close
          </Button>
        </DialogActions>
      </Dialog>
    </GridContainer>
  );
}

export default withStyles(styles)(Products);
