import React, { useState } from "react";
import "./Dashboard.css";
import PropTypes from "prop-types";
// @material-ui/core components
import withStyles from "@material-ui/core/styles/withStyles";
import Icon from "@material-ui/core/Icon";

// @material-ui/icons
// import ContentCopy from "@material-ui/icons/ContentCopy";
import Store from "@material-ui/icons/Store";
// core components
import GridContainer from "../../components/Grid/GridContainer.jsx";
import GridItem from "../../components/Grid/GridItem.jsx";
import Button from "../../components/CustomButtons/Button.jsx";
import Card from "../../components/Card/Card.jsx";
import CardHeader from "../../components/Card/CardHeader.jsx";
import CardIcon from "../../components/Card/CardIcon.jsx";
import CardFooter from "../../components/Card/CardFooter.jsx";
import dashboardStyle from "../../assets/jss/material-dashboard-pro-react/views/dashboardStyle";
import bg from "../../assets/img/smokey_bg.jpg";
import axios from "axios";
import { useEffect } from "react";

let host = process.env.REACT_APP_API_URL;

function Dashboard(props) {
  const [count, setCount] = useState({
    products: 0,
    categories: 0,
    collections: 0,
    sub_categories: 0,
  });

  const [event, setEvent] = useState({
    title: "",
    location: "",
    date: "",
  });

  useEffect(() => {
    fetchCount();
  }, []);

  const fetchCount = async () => {
    try {
      try {
        let res = await axios.get(`${host}/api/admin/dashboard/`);

        if (res) {
          setCount({
            products: res.data.products.count,
            categories: res.data.categories.count,
            collections: res.data.collections.count,
            sub_categories: res.data.sub_categories.count,
          });

          setEvent({
            title: res.data.event.rows[0].title,
            location: res.data.event.rows[0].location,
            date: res.data.event.rows[0].event_date,
          });
        }
      } catch (error) {
        console.log(error);
      }
    } catch (error) {}
  };
  const { classes } = props;
  return (
    <div>
      <GridContainer>
        <GridItem xs={12} sm={6} md={6} lg={3}>
          <Card>
            <CardHeader color="warning" stats icon>
              <CardIcon color="warning">
                <Icon>content_copy</Icon>
              </CardIcon>
              <p className={classes.cardCategory}>Total Products</p>
              <h3 className={classes.cardTitle}>{count.products}</h3>
            </CardHeader>
            <CardFooter stats>
              <div className={classes.stats}>
                {/* <Danger>
                    <Warning />
                  </Danger> */}
                <Button
                  size="small"
                  color="warning"
                  onClick={() => props.history.push("/admin/products")}
                >
                  Manage Product
                </Button>
              </div>
            </CardFooter>
          </Card>
        </GridItem>
        <GridItem xs={12} sm={6} md={6} lg={3}>
          <Card>
            <CardHeader color="success" stats icon>
              <CardIcon color="success">
                <Store />
              </CardIcon>
              <p className={classes.cardCategory}>Total Category</p>
              <h3 className={classes.cardTitle}>{count.categories}</h3>
            </CardHeader>
            <CardFooter stats>
              <div className={classes.stats}>
                {/* <Danger>
                    <Warning />
                  </Danger> */}
                <Button
                  size="small"
                  color="success"
                  onClick={() => props.history.push("/admin/category")}
                >
                  Manage Category
                </Button>
              </div>
            </CardFooter>
          </Card>
        </GridItem>
        <GridItem xs={12} sm={6} md={6} lg={3}>
          <Card>
            <CardHeader color="danger" stats icon>
              <CardIcon color="danger">
                <Icon>info_outline</Icon>
              </CardIcon>
              <p className={classes.cardCategory}>Total Collection</p>
              <h3 className={classes.cardTitle}>{count.collections}</h3>
            </CardHeader>
            <CardFooter stats>
              <div className={classes.stats}>
                {/* <Danger>
                    <Warning />
                  </Danger> */}
                <Button
                  size="small"
                  color="danger"
                  onClick={() => props.history.push("/admin/collection")}
                >
                  Add Collection
                </Button>
              </div>
            </CardFooter>
          </Card>
        </GridItem>
        <GridItem xs={12} sm={6} md={6} lg={3}>
          <Card>
            <CardHeader color="info" stats icon>
              <CardIcon color="info">
                <i className="fab fa-twitter" />
              </CardIcon>
              <p className={classes.cardCategory}>Total Sub Category</p>
              <h3 className={classes.cardTitle}>{count.sub_categories}</h3>
            </CardHeader>
            <CardFooter stats>
              <div className={classes.stats}>
                {/* <Danger>
                    <Warning />
                  </Danger> */}
                <Button
                  size="small"
                  color="info"
                  onClick={() => props.history.push("/admin/subcategory")}
                >
                  Add Sub Category
                </Button>
              </div>
            </CardFooter>
          </Card>
        </GridItem>
      </GridContainer>
      <br />

      <div style={{ backgroundImage: `url(${bg})`, height: "80vh" }}>
        <div>
          <h1
            style={{
              textAlign: "center",
              paddingTop: "20px",
              color: "white",
              fontFamily: "Permanent Marker",
            }}
          >
            {event.title}
          </h1>
          <div style={{ display: "flex", justifyContent: "space-around" }}>
            <h3 style={{ color: "white", fontFamily: "Permanent Marker" }}>
              Location: {event.location}
            </h3>
            <h3 style={{ color: "white", fontFamily: "Permanent Marker" }}>
              Date: {event.date}
            </h3>
          </div>
          <hr />

          <div>
            <h1
              style={{
                textAlign: "center",
                paddingTop: "20px",
                color: "white",
                fontFamily: "Permanent Marker",
              }}
            >
              Top 4 Products
            </h1>
            <br />
            <div style={{ display: "flex", justifyContent: "space-around" }}>
              <img
                src="https://ramdale.s3.amazonaws.com/black-dalamoda.jpg"
                height={320}
              />
              <img
                src="https://ramdale.s3.amazonaws.com/fearless.jpg"
                height={320}
              />
              <img
                src="https://ramdale.s3.amazonaws.com/chico.jpg"
                height={320}
              />
              <img
                src="https://ramdale.s3.amazonaws.com/undefeated.jpg"
                height={320}
              />
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

Dashboard.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(dashboardStyle)(Dashboard);
