import React, { useState, useEffect } from "react";
import PropTypes from "prop-types";
import { withRouter } from "react-router-dom";

// @material-ui/core components
import withStyles from "@material-ui/core/styles/withStyles";
import InputAdornment from "@material-ui/core/InputAdornment";
import Icon from "@material-ui/core/Icon";

// @material-ui/icons
import Email from "@material-ui/icons/Email";
// import LockOutline from "@material-ui/icons/LockOutline";

// core components
import GridContainer from "../../components/Grid/GridContainer.jsx";
import GridItem from "../../components/Grid/GridItem.jsx";
import CustomInput from "../../components/CustomInput/CustomInput.jsx";
import Button from "../../components/CustomButtons/Button.jsx";
import Card from "../../components/Card/Card.jsx";
import CardBody from "../../components/Card/CardBody.jsx";
import CardHeader from "../../components/Card/CardHeader.jsx";
import CardFooter from "../../components/Card/CardFooter.jsx";

import loginPageStyle from "../../assets/jss/material-dashboard-pro-react/views/loginPageStyle.jsx";
import { toast } from "react-toastify";
import "./LoginPage.css";
import axios from "axios";

let host = process.env.REACT_APP_API_URL;

function LoginPage(props) {
  const [state, setstate] = useState({
    cardAnimaton: "",
  });

  const [isLoading, setisLoading] = useState(false);

  const [userData, setuserData] = useState({
    username: "",
    password: "",
  });

  const handleChange = (e) => {
    setuserData({ ...userData, [e.target.name]: e.target.value });
  };

  const isValid = () => {
    if (!userData.username || !userData.password) {
      console.log("EY");
      toast.error("Please input all fields.");

      return false;
    }

    return true;
  };

  const handleSubmit = async () => {
    if (isValid()) {
      setisLoading(true);
      try {
        let res = await axios.post(`${host}/api/admin/auth/signIn`, {
          // category data
          username: userData.username,
          password: userData.password,
        });
        if (res) {
          axios.interceptors.request.use(function (config) {
            config.headers.Authorization = res.data.token;

            return config;
          });
          localStorage.setItem("token", res.data.token);
          localStorage.setItem("role_id", res.data.role_id);
          props.history.push("/admin/dashboard");
        }
      } catch (error) {
        console.log(error);
        setisLoading(false);
        if (error.response.data.error) {
          return toast.error(error.response.data.error);
        }
      }
    }
  };

  useEffect(() => {
    if (localStorage.getItem("token")) {
      props.history.push("/admin/dashboard");
    }
  }, []);

  const { classes } = props;
  return (
    <div className={classes.container}>
      <GridContainer justify="center">
        <GridItem xs={12} sm={6} md={4}>
          <form>
            <Card login className={classes[state.cardAnimaton]}>
              <CardHeader
                className={`${classes.cardHeader} ${classes.textCenter}`}
                color="rose"
              >
                <h4 className={classes.cardTitle}>Log in</h4>
              </CardHeader>
              <CardBody>
                <CustomInput
                  labelText="User"
                  id="username"
                  name="username"
                  formControlProps={{
                    fullWidth: true,
                  }}
                  value={userData.username}
                  onChange={handleChange}
                  inputProps={{
                    endAdornment: (
                      <InputAdornment position="end">
                        <Email className={classes.inputAdornmentIcon} />
                      </InputAdornment>
                    ),
                  }}
                />
                <CustomInput
                  labelText="Password"
                  id="password"
                  name="password"
                  formControlProps={{
                    fullWidth: true,
                  }}
                  value={userData.password}
                  onChange={handleChange}
                  inputProps={{
                    type: "password",
                    endAdornment: (
                      <InputAdornment position="end">
                        <Icon className={classes.inputAdornmentIcon}>
                          lock_outline
                        </Icon>
                      </InputAdornment>
                    ),
                  }}
                />
              </CardBody>
              {isLoading ? (
                <div class="lds-ripple">
                  <div></div>
                  <div></div>
                </div>
              ) : (
                <CardFooter className={classes.justifyContentCenter}>
                  <Button
                    color="rose"
                    simple
                    size="lg"
                    block
                    onClick={handleSubmit}
                  >
                    Let's Go
                  </Button>
                </CardFooter>
              )}
            </Card>
          </form>
        </GridItem>
      </GridContainer>
    </div>
  );
}

LoginPage.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withRouter(withStyles(loginPageStyle)(LoginPage));
