import React, { useState, useEffect } from "react";

// @material-ui/core components
import withStyles from "@material-ui/core/styles/withStyles";

// material-ui icons
import Assignment from "@material-ui/icons/Assignment";
import PanToolIcon from "@material-ui/icons/Check";
import CheckCircleOutlineOutlined from "@material-ui/icons/CheckCircleOutlineOutlined.js";
import NotInterestedOutlined from "@material-ui/icons/NotInterestedOutlined.js";
import Close from "@material-ui/icons/Close";

// core components
import GridContainer from "../Grid/GridContainer.jsx";
import GridItem from "../Grid/GridItem.jsx";
import Table from "../Table/Table.jsx";
import Button from "../CustomButtons/Button.jsx";
import Card from "../Card/Card.jsx";
import CardBody from "../Card/CardBody.jsx";
import CardIcon from "../Card/CardIcon.jsx";
import CardHeader from "../Card/CardHeader.jsx";

import extendedTablesStyle from "../../assets/jss/material-dashboard-pro-react/views/extendedTablesStyle.jsx";
import axios from "axios";
import {
  TextField,
  Tooltip,
  Dialog,
  DialogTitle,
  Slide,
  DialogActions,
  DialogContent,
} from "@material-ui/core";

import { toast } from "react-toastify";
import { useRef } from "react";

let host = process.env.REACT_APP_API_URL;

function Transition(props) {
  return <Slide direction="down" {...props} />;
}

function Attendance(props) {
  const [modal, setmodal] = useState({
    classicModal: false,
  });

  const [filteredDate, setFilteredDate] = useState(new Date());

  let date = new Date();
  let textInput = useRef(null);
  const [state, setstate] = useState({
    user: "",
    name: "",
    email: "",
    password: "",
    confirm_password: "",
  });

  const [orders, setorders] = useState([]);
  const [isLoading, setisLoading] = useState(false);

  const isValid = () => {
    if (!state.name || !state.user || !state.password) {
      toast.error("Please input all fields");
      return false;
    }

    if (state.password != state.confirm_password) {
      toast.error("Password does not match, Please try again.");
      return false;
    }

    return true;
  };

  const handleClose = (modal) => {
    var x = [];
    x[modal] = false;
    setmodal(x);
  };

  const handleChange = (e) => {
    setstate({ ...state, [e.target.name]: e.target.value });
  };

  const handleSubmit = async () => {
    if (isValid()) {
      setisLoading(true);
      try {
        let res = await axios.post(`${host}/api/admin/user/create`, {
          // product data
          name: state.name,
          user: state.user,
          password: state.password,
          email: state.email,
        });
        if (res) {
          fetchAllUsers();
          setmodal(false);
          setisLoading(false);
          toast.success("Employee created successfully");
        }
      } catch (error) {
        setisLoading(false);
        //   console.log(error);
      }
    }
  };

  const fetchAllUsers = async () => {
    let res = await axios.post(`${host}/api/admin/user/timesheet`, {
      timeDate:
        (date.getMonth() > 8
          ? date.getMonth() + 1
          : "0" + (date.getMonth() + 1)) +
        "/" +
        (date.getDate() > 9 ? date.getDate() : "0" + date.getDate()) +
        "/" +
        date.getFullYear(),
    });
    let data = [];
    if (res) {
      for (let i = 0; i < res.data.rows.length; i++) {
        const el = res.data.rows[i];

        data.push([
          roundButtons(el, date),
          el["name"],
          el["email"],
          // el["employee_role_id"],
          "Employee",
          el["time_in"] || "N/A",
          el["time_out"] || "N/A",
        ]);
      }

      setorders(data);
    }
  };

  const fetchEmployeesByDate = async (e) => {
    setFilteredDate(e.target.value);
    let value = e.target.value;
    let res = await axios.post(`${host}/api/admin/user/timesheet`, {
      timeDate:
        (new Date(e.target.value).getMonth() > 8
          ? new Date(e.target.value).getMonth() + 1
          : "0" + (new Date(e.target.value).getMonth() + 1)) +
        "/" +
        (new Date(e.target.value).getDate() > 9
          ? new Date(e.target.value).getDate()
          : "0" + new Date(e.target.value).getDate()) +
        "/" +
        new Date(e.target.value).getFullYear(),
    });
    let data = [];
    if (res) {
      for (let i = 0; i < res.data.rows.length; i++) {
        const el = res.data.rows[i];

        data.push([
          roundButtons(el, new Date(value)),
          el["name"],
          el["email"],
          // el["employee_role_id"],
          "Employee",
          el["time_in"] || "N/A",
          el["time_out"] || "N/A",
        ]);
      }

      setorders(data);
    }
  };

  const handleUser = async (label, order) => {
    console.log(order);
    if (window.confirm(`Are you sure you want to time ${label}?`)) {
      try {
        if (label.toLowerCase() == "in") {
          let outTime = new Date(Date.now() + 8 * (60 * 60 * 1000));
          let res = await axios.post(`${host}/api/admin/user/attendance`, {
            userId: order.id,
            timeDate:
              (date.getMonth() > 8
                ? date.getMonth() + 1
                : "0" + (date.getMonth() + 1)) +
              "/" +
              (date.getDate() > 9 ? date.getDate() : "0" + date.getDate()) +
              "/" +
              date.getFullYear(),
            timeIn: date.toLocaleString("en-US", {
              hour: "numeric",
              minute: "numeric",
              hour12: true,
            }),
            timeOut: outTime.toLocaleString("en-US", {
              hour: "numeric",
              minute: "numeric",
              hour12: true,
            }),
          });

          if (res) {
            fetchAllUsers();
          } else {
            toast.error("Something went wrong, Please try again.");
          }
        } else if (label.toLowerCase() == "out") {
          let outTime = new Date(Date.now() + 8 * (60 * 60 * 1000));
          let res = await axios.post(`${host}/api/admin/user/attendance`, {
            userId: order.id,
            timeDate:
              (date.getMonth() > 8
                ? date.getMonth() + 1
                : "0" + (date.getMonth() + 1)) +
              "/" +
              (date.getDate() > 9 ? date.getDate() : "0" + date.getDate()) +
              "/" +
              date.getFullYear(),
            timeIn: "",
            timeOut: new Date().toLocaleString("en-US", {
              hour: "numeric",
              minute: "numeric",
              hour12: true,
            }),
          });

          if (res) {
            fetchAllUsers();
          } else {
            toast.error("Something went wrong, Please try again.");
          }
        }
      } catch (error) {}
    }
  };

  useEffect(() => {
    fetchAllUsers();
  }, []);

  function isToday(date) {
    const today = new Date();
    //  Today's date

    if (
      today.getFullYear() === date.getFullYear() &&
      today.getMonth() === date.getMonth() &&
      today.getDate() === date.getDate()
    ) {
      return true;
    }

    return false;
  }
  
  const { classes } = props;
  const fillButtons = [
    { color: "info", icon: PanToolIcon },
    { color: "danger", icon: Close },
  ].map((prop, key) => {
    return (
      <Button color={prop.color} className={classes.actionButton} key={key}>
        <prop.icon className={classes.icon} />
      </Button>
    );
  });
  const simpleButtons = [
    { color: "info", icon: PanToolIcon },
    { color: "danger", icon: Close },
  ].map((prop, key) => {
    return (
      <Button
        color={prop.color}
        simple
        className={classes.actionButton}
        key={key}
      >
        <prop.icon className={classes.icon} />
      </Button>
    );
  });
  const roundButtons = (user, date) =>
    [
      { color: "success", icon: CheckCircleOutlineOutlined, label: "IN" },
      { color: "danger", icon: NotInterestedOutlined, label: "OUT" },
    ].map((prop, key) => {
      if (prop.label == "IN") {
        return (
          <Tooltip title={prop.label}>
            <Button
              round
              disabled={user.time_in || !isToday(date)}
              color={prop.color}
              onClick={() => handleUser(prop.label, user)}
              className={classes.actionButton + " " + classes.actionButtonRound}
              key={key}
            >
              <prop.icon className={classes.icon} />
            </Button>
          </Tooltip>
        );
      } else if (prop.label == "OUT") {
        return (
          <Tooltip title={prop.label}>
            <Button
              round
              disabled={!user.time_in}
              color={prop.color}
              onClick={() => handleUser(prop.label, user)}
              className={classes.actionButton + " " + classes.actionButtonRound}
              key={key}
            >
              <prop.icon className={classes.icon} />
            </Button>
          </Tooltip>
        );
      }
    });
  return (
    <GridContainer>
      <GridItem xs={12}>
        <Card>
          <CardHeader color="rose" icon>
            <CardIcon color="rose">
              <Assignment />
            </CardIcon>
            <h4 className={classes.cardIconTitle}>Attendance</h4>
            <br />
            <GridContainer>
              <GridItem xs={12} sm={12} md={2}>
                <TextField
                  id="date"
                  label="Filter Date"
                  type="date"
                  inputRef={textInput}
                  name="delivery_date"
                  defaultValue={new Date().toLocaleDateString("en-CA")}
                  onChange={fetchEmployeesByDate}
                  className={classes.textField}
                  InputLabelProps={{
                    shrink: true,
                  }}
                />
              </GridItem>
            </GridContainer>
            {/* <GridContainer>
              <Button
                onClick={() => handleClickOpen("classicModal")}
                style={{ marginLeft: "10px" }}
              >
                Create Employee
              </Button>
            </GridContainer> */}
          </CardHeader>
          <CardBody>
            <Table
              tableHead={[
                "Actions",
                "Name",
                "Email",
                "Role",
                "Time In",
                "Time Out",
              ]}
              tableData={orders}
              customCellClasses={[classes.center, classes.right, classes.right]}
              customClassesForCells={[4, 4, 4, 4, 4, 4, 4, 0, 4]}
              customHeadCellClasses={[
                classes.center,
                classes.right,
                classes.right,
              ]}
              customHeadClassesForCells={[4, 4, 4, 4, 4, 4, 4, 0, 4]}
            />
          </CardBody>
        </Card>
      </GridItem>

      <Dialog
        fullWidth
        maxWidth="sm"
        open={modal.classicModal}
        TransitionComponent={Transition}
        keepMounted
        onClose={() => handleClose("classicModal")}
        aria-labelledby="classic-modal-slide-title"
        aria-describedby="classic-modal-slide-description"
      >
        <DialogTitle
          id="classic-modal-slide-title"
          disableTypography
          className={classes.modalHeader}
        >
          Create Employee
        </DialogTitle>
        <DialogContent>
          <TextField
            autoFocus
            margin="dense"
            id="name"
            label="Username"
            name="user"
            type="text"
            fullWidth
            variant="standard"
            onChange={handleChange}
          />
          <TextField
            autoFocus
            margin="dense"
            id="name"
            label="Full Name"
            name="name"
            type="text"
            fullWidth
            variant="standard"
            onChange={handleChange}
          />
          <TextField
            autoFocus
            margin="dense"
            id="name"
            label="Email"
            name="email"
            type="email"
            fullWidth
            variant="standard"
            onChange={handleChange}
          />
          <TextField
            autoFocus
            margin="dense"
            id="name"
            label="Password"
            type="password"
            name="password"
            fullWidth
            variant="standard"
            onChange={handleChange}
          />
          <TextField
            autoFocus
            margin="dense"
            id="name"
            label="Confirm Password"
            type="password"
            name="confirm_password"
            fullWidth
            variant="standard"
            onChange={handleChange}
          />
        </DialogContent>

        <DialogActions className={classes.modalFooter}>
          <Button
            // onClick={() => handleClose("classicModal")}
            disabled={isLoading}
            color="success"
            simple
            onClick={handleSubmit}
          >
            Submit
          </Button>
          <Button
            onClick={() => handleClose("classicModal")}
            disabled={isLoading}
            color="danger"
            simple
          >
            Close
          </Button>
        </DialogActions>
      </Dialog>
    </GridContainer>
  );
}

export default withStyles(extendedTablesStyle)(Attendance);
