import React, { useState, useEffect } from "react";

// @material-ui/core components
import withStyles from "@material-ui/core/styles/withStyles";
import FormLabel from "@material-ui/core/FormLabel";
import FormControlLabel from "@material-ui/core/FormControlLabel";

// core components
import GridContainer from "../../components/Grid/GridContainer.jsx";
import GridItem from "../../components/Grid/GridItem.jsx";
import CustomInput from "../../components/CustomInput/CustomInput.jsx";
import Card from "../../components/Card/Card.jsx";
import CardHeader from "../../components/Card/CardHeader.jsx";
import CardText from "../../components/Card/CardText.jsx";
import CardBody from "../../components/Card/CardBody.jsx";

import regularFormsStyle from "../../assets/jss/material-dashboard-pro-react/views/regularFormsStyle";
import { FormControl, RadioGroup, Radio } from "@material-ui/core";
import Button from "../../components/CustomButtons/Button.jsx";
// import "./Product.css";
import axios from "axios";
import { toast } from "react-toastify";

let host = process.env.REACT_APP_API_URL;

function CreateSubCategory(props) {
  const [subcat, setsubcat] = useState({
    name: "",
    slug: "",
    category_id: "",
  });

  const [categories, setcategories] = useState(null);
  const [isLoading, setisLoading] = useState(false);

  const handleChange = (e) => {
    setsubcat({ ...subcat, [e.target.name]: e.target.value });
  };

  const isValid = () => {
    if (!subcat.name || !subcat.slug || !subcat.category_id) {
      toast.success("Please input all fields");
      return false;
    }

    return true;
  };

  const fetchCategories = async () => {
    try {
      let res = await axios.get(`${host}/api/admin/category`);

      if (res) {
        setcategories(res.data.categories);
      }
    } catch (error) {
      //   console.log(error);
    }
  };

  const handleSubmit = async () => {
    if (isValid()) {
      setisLoading(true);
      try {
        let res = await axios.post(`${host}/api/admin/sub_category`, {
          // product data
          name: subcat.name,
          slug: subcat.slug,
          category_id: subcat.category_id,
        });
        if (res) {
          setsubcat({
            name: "",
            slug: "",
            category_id: "",
          });
          toast.success("Category created succesfully.");
          props.handleClose("classicModal");
          setisLoading(false);
        }
      } catch (error) {
        setisLoading(false);
        //   console.log(error);
      }
    }
  };

  useEffect(() => {
    fetchCategories();
  }, []);

  const { classes } = props;

  if (categories) {
    return (
      <GridContainer style={{ minWidth: "1000px" }}>
        <GridItem xs={12} sm={12} md={12}>
          <Card>
            <CardHeader color="rose" text>
              <CardText color="rose">
                <h6 className={classes.cardTitle}>Create Sub Category</h6>
              </CardText>
            </CardHeader>
            <CardBody>
              <form>
                <GridContainer>
                  <GridItem xs={12} sm={2}>
                    <FormLabel className={classes.labelHorizontal}>
                      Categories
                    </FormLabel>
                  </GridItem>
                  <GridItem xs={12} sm={2}>
                    <FormControl component="fieldset">
                      <RadioGroup
                        aria-label="gender"
                        name="category_id"
                        // value={value}
                        onChange={handleChange}
                      >
                        {categories &&
                          categories.map((cat, i) => (
                            <FormControlLabel
                              key={i}
                              value={cat.id.toString()}
                              control={<Radio />}
                              label={cat.name.toString()}
                            />
                          ))}
                      </RadioGroup>
                    </FormControl>
                  </GridItem>
                </GridContainer>

                <GridContainer>
                  <GridItem xs={12} sm={2}>
                    <FormLabel className={classes.labelHorizontal}>
                      Sub Category Name
                    </FormLabel>
                  </GridItem>
                  <GridItem xs={12} sm={10}>
                    <CustomInput
                      id="title"
                      formControlProps={{
                        fullWidth: true,
                      }}
                      inputProps={{
                        placeholder: "Sub Category Name",
                      }}
                      value={subcat.name}
                      name="name"
                      onChange={handleChange}
                    />
                  </GridItem>
                </GridContainer>

                <GridContainer>
                  <GridItem xs={12} sm={2}>
                    <FormLabel className={classes.labelHorizontal}>
                      Sub Category Slug
                    </FormLabel>
                  </GridItem>
                  <GridItem xs={12} sm={10}>
                    <CustomInput
                      id="title"
                      formControlProps={{
                        fullWidth: true,
                      }}
                      inputProps={{
                        placeholder: "Sub Category Slug",
                      }}
                      value={subcat.slug}
                      name="slug"
                      onChange={handleChange}
                    />
                  </GridItem>
                </GridContainer>

                <div style={{ float: "right" }}>
                  <Button
                    color="success"
                    className={classes.marginRight}
                    onClick={handleSubmit}
                  >
                    {isLoading ? (
                      <>
                        <div class="lds-ring">
                          <div />
                          <div />
                          <div />
                          <div />
                        </div>{" "}
                      </>
                    ) : (
                      "Submit"
                    )}
                  </Button>
                </div>
              </form>
            </CardBody>
          </Card>
        </GridItem>
      </GridContainer>
    );
  } else {
    return <h1>Loading, please wait</h1>;
  }
}

export default withStyles(regularFormsStyle)(CreateSubCategory);
