import React, { useState, useEffect, useRef } from "react";

// @material-ui/core components
import withStyles from "@material-ui/core/styles/withStyles";

// material-ui icons
import Assignment from "@material-ui/icons/Assignment";
import PanToolIcon from "@material-ui/icons/Check";
import Close from "@material-ui/icons/Close";

// core components
import GridContainer from "../../components/Grid/GridContainer.jsx";
import GridItem from "../../components/Grid/GridItem.jsx";
import Table from "../../components/Table/Table.jsx";
import Button from "../../components/CustomButtons/Button.jsx";
import Card from "../../components/Card/Card.jsx";
import CardBody from "../../components/Card/CardBody.jsx";
import CardIcon from "../../components/Card/CardIcon.jsx";
import CardHeader from "../../components/Card/CardHeader.jsx";

import extendedTablesStyle from "../../assets/jss/material-dashboard-pro-react/views/extendedTablesStyle.jsx";
import axios from "axios";
import {
  TextField,
  Tooltip,
  Dialog,
  DialogTitle,
  Slide,
  DialogActions,
  DialogContent,
} from "@material-ui/core";
import { debounce } from "../../functions.js";
import { toast } from "react-toastify";

let host = process.env.REACT_APP_API_URL;

function Transition(props) {
  return <Slide direction="down" {...props} />;
}

function CustomizedOrders(props) {
  const [modal, setmodal] = useState({
    classicModal: false,
  });

  let textInput = useRef(null);
  const [state, setstate] = useState({
    order_name: "",
    delivery_date: "",
  });

  const [orders, setorders] = useState([]);
  const [myOrder, setmyOrder] = useState(null);

  const handleClickOpen = async (modal, order) => {
    setmyOrder(order);
    var x = [];
    x[modal] = true;
    setmodal(x);
  };

  const handleClose = (modal) => {
    var x = [];
    x[modal] = false;
    setmodal(x);
  };

  const fetchAllOrders = async () => {
    let res = await axios.get(`${host}/api/admin/order/custom-orders`);
    let data = [];
    if (res) {
      for (let i = 0; i < res.data.orders.rows.length; i++) {
        const el = res.data.orders.rows[i];

        data.push([
          roundButtons(el),
          <a onClick={() => handleClickOpen("classicModal", el)}>
            Custom Order #{i + 1}
          </a>,
          el["email"],
          el["details"],
          el["contact"],
          el["order_status"]["name"],
          el["created_at"],
        ]);
      }

      setorders(data);
    }
  };

  const handleOrder = async (label, order) => {
    if (window.confirm(`Are you sure you want to ${label} this order?`)) {
      try {
        if (label == "cancel") {
          let res = await axios.patch(
            `${host}/api/admin/order/custom-order/${order["id"]}`,
            {
              order_status_id: 9,
            }
          );

          if (res) {
            setorders((prevState) => {
              let newOrders = prevState.map((el) =>
                el[1] == order["order_name"]
                  ? [...el, (el[4] = "canceled")]
                  : el
              );

              return [...prevState];
            });
            toast.success("Order updated successfully");
          }
        }
      } catch (error) {}
    }
  };

  useEffect(() => {
    fetchAllOrders();
  }, []);

  const { classes } = props;
  const fillButtons = [
    { color: "info", icon: PanToolIcon },
    { color: "danger", icon: Close },
  ].map((prop, key) => {
    return (
      <Button color={prop.color} className={classes.actionButton} key={key}>
        <prop.icon className={classes.icon} />
      </Button>
    );
  });
  const simpleButtons = [
    { color: "info", icon: PanToolIcon },
    { color: "danger", icon: Close },
  ].map((prop, key) => {
    return (
      <Button
        color={prop.color}
        simple
        className={classes.actionButton}
        key={key}
      >
        <prop.icon className={classes.icon} />
      </Button>
    );
  });
  const roundButtons = (order_id) =>
    [
      // { color: "info", icon: PanToolIcon, label: "accept" },
      // { color: "info", icon: PaymentIcon, label: "paid" },
      { color: "danger", icon: Close, label: "cancel" },
    ].map((prop, key) => {
      return (
        <Tooltip title={prop.label}>
          <Button
            round
            color={prop.color}
            onClick={() => handleOrder(prop.label, order_id)}
            className={classes.actionButton + " " + classes.actionButtonRound}
            key={key}
          >
            <prop.icon className={classes.icon} />
          </Button>
        </Tooltip>
      );
    });
  return (
    <GridContainer>
      <GridItem xs={12}>
        <Card>
          <CardHeader color="rose" icon>
            <CardIcon color="rose">
              <Assignment />
            </CardIcon>
            <h4 className={classes.cardIconTitle}>Customized Orders</h4>
          </CardHeader>
          <CardBody>
            <Table
              tableHead={[
                "Actions",
                "Title",
                "Email",
                "Details",
                "Contact",
                "Order Status",
                // "Delivery",
                "Created",
              ]}
              tableData={orders}
              customCellClasses={[classes.center, classes.right, classes.right]}
              customClassesForCells={[0, 4, 5]}
              customHeadCellClasses={[
                classes.center,
                classes.right,
                classes.right,
              ]}
              customHeadClassesForCells={[0, 4, 5]}
            />
          </CardBody>
        </Card>
      </GridItem>

      <Dialog
        fullWidth={true}
        maxWidth="lg"
        classes={{
          root: classes.center + " " + classes.modalRoot,
          paper: classes.modal,
          scrollPaper: classes.scrollPaper,
        }}
        open={modal.classicModal}
        TransitionComponent={Transition}
        keepMounted
        onClose={() => handleClose("classicModal")}
        aria-labelledby="classic-modal-slide-title"
        aria-describedby="classic-modal-slide-description"
      >
        <DialogTitle
          id="classic-modal-slide-title"
          disableTypography
          className={classes.modalHeader}
        >
          <Button
            justIcon
            className={classes.modalCloseButton}
            key="close"
            aria-label="Close"
            color="transparent"
            onClick={() => handleClose("classicModal")}
          >
            <Close className={classes.modalClose} />
          </Button>
        </DialogTitle>
        <DialogContent>
          {myOrder ? (
            <>
              <h3>Customized Order</h3>
              <hr />
              <h5>Email: {myOrder.email}</h5>
              <h5>Contact Number: {myOrder.contact}</h5>
              <h5>Details: {myOrder.details}</h5>
              <hr />
              {/* <h5>Order Items</h5> */}
              {JSON.parse(myOrder["images"]).map((img, i) => {
                return (
                  <>
                    <img height={180} src={img} />
                  </>
                );
              })}
            </>
          ) : (
            <h4>Loading, please wait...</h4>
          )}
        </DialogContent>

        <DialogActions className={classes.modalFooter}>
          <Button
            onClick={() => handleClose("classicModal")}
            color="danger"
            simple
          >
            Close
          </Button>
        </DialogActions>
      </Dialog>
    </GridContainer>
  );
}

export default withStyles(extendedTablesStyle)(CustomizedOrders);
