import React, { useState, useEffect } from "react";
// react component for creating dynamic tables
import ReactTable from "react-table";
import Edit from "@material-ui/icons/Edit";

// @material-ui/core components
import withStyles from "@material-ui/core/styles/withStyles";
import Slide from "@material-ui/core/Slide";
import Dialog from "@material-ui/core/Dialog";
import DialogTitle from "@material-ui/core/DialogTitle";
import DialogActions from "@material-ui/core/DialogActions";

// @material-ui/icons
import Assignment from "@material-ui/icons/Assignment";
import Close from "@material-ui/icons/Close";
import PanTool from "@material-ui/icons/PanTool";
// core components
import GridContainer from "../../components/Grid/GridContainer.jsx";
import GridItem from "../../components/Grid/GridItem.jsx";
import Button from "../../components/CustomButtons/Button.jsx";
import Card from "../../components/Card/Card.jsx";
import CardBody from "../../components/Card/CardBody.jsx";
import CardIcon from "../../components/Card/CardIcon.jsx";
import CardHeader from "../../components/Card/CardHeader.jsx";
import { cardTitle } from "../../assets/jss/material-dashboard-pro-react.jsx";
import { Switch, DialogContent } from "@material-ui/core";
import axios from "axios";
import CollectionProduct from "./CollectionProduct";
import CreateCollection from "./CreateCollection";
import EditCollection from "./EditCollection";
import CreateCategory from "../Category/CreateCategory";
import { toast } from "react-toastify";

const styles = {
  cardIconTitle: {
    ...cardTitle,
    marginTop: "15px",
    marginBottom: "0px",
    scrollPaper: {
      alignItems: "baseline", // default center
    },
  },
};

let host = process.env.REACT_APP_API_URL;

function Transition(props) {
  return <Slide direction="down" {...props} />;
}

function Collection(props) {
  const { classes } = props;

  const [modal, setmodal] = useState({
    classicModal: false,
  });

  const [willDo, setwillDo] = useState(false);
  const [selectedCat, setselectedCat] = useState(null);
  const [products, setproducts] = useState(null);

  const [state, setstate] = useState({
    data: null,
  });

  const handleClickOpen = (modal) => {
    var x = [];
    x[modal] = true;
    setmodal(x);
  };

  const handleClose = (modal) => {
    var x = [];
    x[modal] = false;
    setmodal(x);
    fetchCollections();
  };

  const handleToggle = async (id) => {
    try {
      let res = await axios.patch(`${host}/api/admin/collection/status/${id}`);
      if (res) {
        toast.success("Collection updated successfully!");
        fetchCollections();
      }
    } catch (error) {
      toast.error("Something went wrong, Please try again.");
      console.log(error);
    }
  };

  const deleteProduct = async (id) => {
    if (window.confirm("Are you sure you want to delete this collection?")) {
      try {
        let res = await axios.delete(`${host}/api/admin/collection/${id}`);
        if (res) {
          fetchCollections();
        }
      } catch (error) {
        toast.error("Something went wrong, Please try again.");
        console.log(error);
      }
    }
  };

  const fetchProducts = async () => {
    try {
      let res = await axios.get(`${host}/api/admin/product/`);

      if (res) {
        setproducts(res.data.products);
      }
    } catch (error) {
      console.log(error);
    }
  };

  const fetchCollections = async () => {
    try {
      try {
        let res = await axios.get(`${host}/api/admin/collection/`);

        if (res) {
          // setproducts();
          setstate({
            ...state,
            data: res.data.collections.map((prop, key) => {
              return {
                id: prop.id,
                name: prop.title,
                published: (
                  <Switch
                    defaultChecked={prop.status ? true : false}
                    onChange={() => handleToggle(prop.id)}
                    color="primary"
                    name="checkedB"
                    inputProps={{ "aria-label": "primary checkbox" }}
                  />
                ),
                actions: (
                  // we've added some custom button actions
                  <div className="actions-right">
                    {/* use this button to add a like kind of action */}
                    <Button
                      justIcon
                      round
                      simple
                      onClick={() => {
                        setselectedCat(prop);
                        setwillDo("manage");
                        handleClickOpen("classicModal");
                      }}
                      color="warning"
                      className="edit"
                    >
                      <PanTool />
                    </Button>
                    <Button
                      justIcon
                      round
                      simple
                      onClick={() => {
                        setselectedCat(prop);
                        setwillDo("edit");
                        handleClickOpen("classicModal");
                      }}
                      color="warning"
                      className="edit"
                    >
                      <Edit />
                    </Button>
                    {/* use this button to remove the data row */}
                    <Button
                      justIcon
                      round
                      simple
                      onClick={() => {
                        deleteProduct(prop.id);
                      }}
                      color="danger"
                      className="remove"
                    >
                      <Close />
                    </Button>{" "}
                  </div>
                ),
              };
            }),
          });
        }
      } catch (error) {
        console.log(error);
      }
    } catch (error) {}
  };

  useEffect(() => {
    fetchProducts();
    fetchCollections();
  }, []);

  return (
    <GridContainer>
      <GridItem xs={12}>
        <Card>
          <CardHeader color="primary" icon>
            <CardIcon color="primary">
              <Assignment />
            </CardIcon>
            <div style={{ display: "flex", justifyContent: "space-between" }}>
              <h4 className={classes.cardIconTitle}>Collection</h4>
              <div style={{ marginTop: "0.5rem" }}>
                <Button
                  onClick={() => {
                    setwillDo("add");
                    handleClickOpen("classicModal");
                  }}
                >
                  Add New Collection
                </Button>
              </div>
            </div>
          </CardHeader>
          <CardBody>
            {state.data ? (
              <ReactTable
                data={state.data}
                filterable
                columns={[
                  {
                    Header: "ID",
                    accessor: "id",
                    maxWidth: "100",
                  },
                  {
                    Header: "Collection",
                    accessor: "name",
                  },
                  {
                    Header: "Published",
                    accessor: "published",
                    maxWidth: "100",
                  },
                  {
                    Header: "Actions",
                    accessor: "actions",
                    sortable: false,
                    filterable: false,
                  },
                ]}
                defaultPageSize={10}
                showPaginationTop
                showPaginationBottom={false}
                className="-striped -highlight"
              />
            ) : (
              <h2>Loading, please wait...</h2>
            )}
          </CardBody>
        </Card>
      </GridItem>

      <Dialog
        maxWidth="lg"
        classes={{
          root: classes.center + " " + classes.modalRoot,
          paper: classes.modal,
          scrollPaper: classes.scrollPaper,
        }}
        open={modal.classicModal}
        TransitionComponent={Transition}
        keepMounted
        onClose={() => handleClose("classicModal")}
        aria-labelledby="classic-modal-slide-title"
        aria-describedby="classic-modal-slide-description"
      >
        <DialogTitle
          id="classic-modal-slide-title"
          disableTypography
          className={classes.modalHeader}
        >
          <Button
            justIcon
            className={classes.modalCloseButton}
            key="close"
            aria-label="Close"
            color="transparent"
            onClick={() => handleClose("classicModal")}
          >
            <Close className={classes.modalClose} />
          </Button>
          {/* <h4 className={classes.modalTitle}>{product.name}</h4> */}
        </DialogTitle>

        {willDo == "add" && <CreateCollection handleClose={handleClose} />}
        {willDo == "edit" && (
          <EditCollection handleClose={handleClose} collection={selectedCat} />
        )}
        {willDo == "manage" && (
          <CollectionProduct
            products={products}
            handleClose={handleClose}
            collection={selectedCat}
          />
        )}

        {/* <div style={{ height: "10rem" }} /> */}
        <DialogActions className={classes.modalFooter}>
          <Button
            onClick={() => handleClose("classicModal")}
            color="danger"
            simple
          >
            Close
          </Button>
        </DialogActions>
      </Dialog>
    </GridContainer>
  );
}

export default withStyles(styles)(Collection);
