import React, { useState, useEffect } from "react";
import { NavLink } from "react-router-dom";
// react component for creating dynamic tables
import ReactTable from "react-table";
import Edit from "@material-ui/icons/Edit";
import { withRouter } from "react-router-dom";
// @material-ui/core components
import withStyles from "@material-ui/core/styles/withStyles";
// @material-ui/icons
import Assignment from "@material-ui/icons/Assignment";
import Close from "@material-ui/icons/Close";
// core components
import GridContainer from "../../components/Grid/GridContainer.jsx";
import GridItem from "../../components/Grid/GridItem.jsx";
import Button from "../../components/CustomButtons/Button.jsx";
import Card from "../../components/Card/Card.jsx";
import CardBody from "../../components/Card/CardBody.jsx";
import CardIcon from "../../components/Card/CardIcon.jsx";
import CardHeader from "../../components/Card/CardHeader.jsx";
import { cardTitle } from "../../assets/jss/material-dashboard-pro-react.jsx";
import { Switch } from "@material-ui/core";
import axios from "axios";
import ProductImage from "../ProductImage/ProductImage";
import { toast } from "react-toastify";

const styles = {
  cardIconTitle: {
    ...cardTitle,
    marginTop: "15px",
    marginBottom: "0px",
  },
};

let host = process.env.REACT_APP_API_URL;

function Products(props) {
  const { classes } = props;

  const deleteProduct = async (id) => {
    if (window.confirm("Are you sure you want to delete this product?")) {
      try {
        let res = await axios.delete(`${host}/api/admin/product/${id}`);
        if (res) {
          fetchProducts();
        }
      } catch (error) {
        toast.error("Something went wrong, Please try again.");
        console.log(error);
      }
    }
  };

  const handleToggle = async (id) => {
    try {
      let res = await axios.patch(`${host}/api/admin/product/published/${id}`);
      if (res) {
        toast.success("Product updated successfully!");
        fetchProducts();
      }
    } catch (error) {
      toast.error("Something went wrong, Please try again.");
      console.log(error);
    }
  };

  const fetchProducts = async () => {
    try {
      try {
        let res = await axios.get(`${host}/api/admin/product/`);

        if (res) {
          // setproducts();
          setstate({
            ...state,
            data: res.data.products.map((prop, key) => {
              return {
                id: prop.id,
                name: prop.name,
                photo: <ProductImage productId={prop.id} theProduct={prop} />,
                type: prop.type,
                price: (
                  <>
                    <p>Regular Price: {prop.regular_price}</p>
                    <p>Discount Price: {prop.discount_price}</p>
                  </>
                ),
                published: (
                  <Switch
                    defaultChecked={prop.published ? true : false}
                    onChange={() => handleToggle(prop.id)}
                    color="primary"
                    name="checkedB"
                    inputProps={{ "aria-label": "primary checkbox" }}
                  />
                ),
                actions: (
                  // we've added some custom button actions
                  <div className="actions-right">
                    {/* use this button to add a like kind of action */}
                    <NavLink
                      to={`/admin/product/edit/${prop.id}`}
                      className={classes.navLink}
                    >
                      <Button
                        justIcon
                        round
                        simple
                        // onClick={}
                        color="warning"
                        className="edit"
                      >
                        <Edit />
                      </Button>
                    </NavLink>
                    {/* use this button to remove the data row */}
                    <Button
                      justIcon
                      round
                      simple
                      onClick={() => {
                        deleteProduct(prop.id);
                      }}
                      color="danger"
                      className="remove"
                    >
                      <Close />
                    </Button>{" "}
                  </div>
                ),
              };
            }),
          });
        }
      } catch (error) {
        console.log(error);
      }
    } catch (error) {}
  };

  useEffect(() => {
    fetchProducts();
  }, []);

  const [state, setstate] = useState({
    data: null,
  });

  return (
    <GridContainer>
      <GridItem xs={12}>
        <Card>
          <CardHeader color="primary" icon>
            <CardIcon color="primary">
              <Assignment />
            </CardIcon>
            <div style={{ display: "flex", justifyContent: "space-between" }}>
              <h4 className={classes.cardIconTitle}>Products</h4>
              <div style={{ marginTop: "0.5rem" }}>
                <Button
                  onClick={() => props.history.push("/admin/product/create")}
                >
                  Add New Product
                </Button>
              </div>
            </div>
          </CardHeader>
          <CardBody>
            {state.data ? (
              <ReactTable
                data={state.data}
                filterable
                columns={[
                  {
                    Header: "ID",
                    accessor: "id",
                    maxWidth: "100",
                  },
                  {
                    Header: "Name",
                    accessor: "name",
                  },
                  {
                    Header: "Photo",
                    accessor: "photo",
                    maxWidth: "200",
                  },
                  {
                    Header: "Type",
                    accessor: "type",
                    maxWidth: "100",
                  },
                  {
                    Header: "Price",
                    accessor: "price",
                  },
                  {
                    Header: "Published",
                    accessor: "published",
                    maxWidth: "100",
                  },
                  {
                    Header: "Actions",
                    accessor: "actions",
                    sortable: false,
                    filterable: false,
                  },
                ]}
                defaultPageSize={10}
                showPaginationTop
                showPaginationBottom={false}
                className="-striped -highlight"
              />
            ) : (
              <h2>Loading, please wait</h2>
            )}
          </CardBody>
        </Card>
      </GridItem>
    </GridContainer>
  );
}

export default withRouter(withStyles(styles)(Products));
