/*eslint-disable*/
import React, { useState, useEffect } from "react";
import ImageUploader from "react-images-upload";
// @material-ui/core components
import withStyles from "@material-ui/core/styles/withStyles";
import Slide from "@material-ui/core/Slide";
import Dialog from "@material-ui/core/Dialog";
import DialogTitle from "@material-ui/core/DialogTitle";
import DialogContent from "@material-ui/core/DialogContent";
import DialogActions from "@material-ui/core/DialogActions";

// @material-ui/icons
import Close from "@material-ui/icons/Close";

// core components
import FormControlLabel from "@material-ui/core/FormControlLabel";
import Radio from "@material-ui/core/Radio";
import GridContainer from "../../components/Grid/GridContainer.jsx";
import GridItem from "../../components/Grid/GridItem.jsx";
import Button from "../../components/CustomButtons/Button.jsx";

import notificationsStyle from "../../assets/jss/material-dashboard-pro-react/views/notificationsStyle.jsx";

import axios from "axios";
import "./ProductImage.css";
import { FormControl, RadioGroup } from "@material-ui/core";
import { toast } from "react-toastify";


let host = process.env.REACT_APP_API_URL;

function Transition(props) {
  return <Slide direction="down" {...props} />;
}

function ProductImage(props) {
  const [state, setstate] = useState({
    classicModal: false,
  });

  const [isUploading, setisUploading] = useState(false);
  const [isDeleting, setisDeleting] = useState(false);

  const [willDo, setwillDo] = useState(false);
  const [ImageUploaderKey, setImageUploaderKey] = useState(0);
  let [product, setproduct] = useState(props.theProduct);
  const [thumb, setthumb] = useState(null);

  let formData = new FormData();

  const onDrop = (pictureFiles) => {
    if (pictureFiles.length > 0) {
      for (let i = 0; i < pictureFiles.length; i++) {
        const el = pictureFiles[i];

        formData.append("image", el);
      }
    }
  };

  const fetchProducts = async () => {
    try {
      let res = await axios.get(
        `${host}/api/admin/product/by?id=${props.productId}`
      );

      if (res) {
        setproduct(res.data.product);
      }
    } catch (error) {
      //   console.log(error);
    }
  };

  useEffect(() => {
    var id = window.setTimeout(null, 0);
    while (id--) {
      window.clearTimeout(id);
    }
    return () => {
      setImageUploaderKey(ImageUploaderKey + 1);
      setwillDo(false);
    };
  }, []);

  const handleClickOpen = (modal) => {
    var x = [];
    x[modal] = true;
    setstate(x);
  };

  const handleClose = (modal) => {
    var x = [];
    x[modal] = false;
    setstate(x);
    setImageUploaderKey(ImageUploaderKey + 1);
    setwillDo(false);
  };

  const handleSubmit = async () => {
    setisUploading(true);
    try {
      let res = await axios.post(
        `${host}/api/admin/productimage/${product.id}`,
        formData,
        {
          headers: {
            "Content-Type": "multipart/form-data",
          },
        }
      );
      if (res) {
        toast.success("Image uploaded successfully.");
        fetchProducts();
        setwillDo("edit");
        setisUploading(false);
      }
    } catch (error) {
      console.log(error);
    }
  };

  const deleteImage = async (id, thumbnail) => {
    if (confirm("Are you sure you want to delete this image?")) {
      if (thumbnail) {
        toast.error(
          "You can't delete thumbnail product. Please assign it to the other image."
        );
      } else {
        setisDeleting(true);
        try {
          let res = await axios.delete(`${host}/api/admin/productimage/${id}`);
          if (res) {
            fetchProducts();
            setisDeleting(false);
          }
        } catch (error) {
          toast.error("Something went wrong, Please try again.");
          console.log(error);
        }
      }
    }
  };

  const handleThumbnail = async (id) => {
    if (thumb) {
      if (confirm("Are you sure you want make this image the thumbnail?")) {
        try {
          let res = await axios.patch(
            `${host}/api/admin/productimage/setthumbnail/${thumb}`
          );
          if (res) {
            fetchProducts();
          }
        } catch (error) {
          console.log(error);
        }
      }
    }
  };

  useEffect(() => {
    if (thumb) {
      handleThumbnail();
    }
  }, [thumb]);

  useEffect(() => {
    setproduct(props.theProduct);
  }, [props.theProduct]);

  const dialogContent = () => {
    if (willDo == "add") {
      return (
        <ImageUploader
          key={ImageUploaderKey}
          withIcon={true}
          buttonText="Choose images"
          onChange={onDrop}
          imgExtension={[".jpg", ".gif", ".png", ".gif"]}
          maxFileSize={5242880}
          withPreview={true}
        />
      );
    } else if (willDo == "edit") {
      if (product.product_images.length) {
        return (
          <FormControl component="fieldset">
            <br />
            <GridContainer>
              <RadioGroup
                aria-label="gender"
                name="gender1"
                row
                // value={value}
                // onChange={handleChange}
              >
                {product.product_images.map((item, i) => (
                  <FormControlLabel
                    key={i}
                    value={item.id.toString()}
                    control={
                      <Radio
                        checked={
                          thumb
                            ? thumb == item.id
                            : item.thumbnail == 1
                            ? true
                            : false
                        }
                      />
                    }
                    onChange={() => {
                      setthumb(item.id);
                    }}
                    label={
                      <GridItem xs={4} key={i}>
                        <img
                          className="image"
                          onClick={() => handleClickOpen("classicModal")}
                          src={`https://ramdale.s3.amazonaws.com/${item["file_name"]}`}
                          height="100"
                          alt="image"
                        />
                        <br />
                        <Button
                          color="danger"
                          size={"sm"}
                          onClick={() => deleteImage(item.id, item.thumbnail)}
                        >
                          Delete
                        </Button>
                        <br />
                      </GridItem>
                    }
                  />
                ))}
                {/* <FormControlLabel
                  value="female"
                  control={<Radio />}
                  label="Female"
                />
                <FormControlLabel
                  value="male"
                  control={<Radio />}
                  label="Male"
                />
                <FormControlLabel
                  value="other"
                  control={<Radio />}
                  label="Other"
                /> */}
              </RadioGroup>
            </GridContainer>
          </FormControl>
        );
      } else {
        return <h2>No Images found</h2>;
      }
    }
  };

  const { classes } = props;
  let thumbnail = product.product_images.filter((item) => item.thumbnail == 1);
  if (product) {
    return (
      <div>
        <GridContainer justify="center">
          <GridItem xs={12} sm={12} md={12} className={classes.center}>
            {thumbnail.length ? (
              <img
                className="image"
                onClick={() => handleClickOpen("classicModal")}
                src={`https://ramdale.s3.amazonaws.com/${thumbnail[0]["file_name"]}`}
                height="100"
                alt="image"
              />
            ) : (
              <Button onClick={() => handleClickOpen("classicModal")}>
                Add Image
              </Button>
            )}
            <Dialog
              maxWidth="lg"
              classes={{
                root: classes.center + " " + classes.modalRoot,
                paper: classes.modal,
              }}
              open={state.classicModal}
              TransitionComponent={Transition}
              keepMounted
              onClose={() => handleClose("classicModal")}
              aria-labelledby="classic-modal-slide-title"
              aria-describedby="classic-modal-slide-description"
            >
              <DialogTitle
                id="classic-modal-slide-title"
                disableTypography
                className={classes.modalHeader}
              >
                <Button
                  justIcon
                  className={classes.modalCloseButton}
                  key="close"
                  aria-label="Close"
                  color="transparent"
                  onClick={() => handleClose("classicModal")}
                >
                  <Close className={classes.modalClose} />
                </Button>
                <h4 className={classes.modalTitle}>{product.name}</h4>
              </DialogTitle>
              <DialogContent
                id="classic-modal-slide-description"
                className={classes.modalBody}
              >
                <div style={{ display: "block" }}>
                  <Button color="warning" onClick={() => setwillDo("add")}>
                    Add Image
                  </Button>
                  <Button color="success" onClick={() => setwillDo("edit")}>
                    Edit Image
                  </Button>
                </div>
                {dialogContent()}
                {isDeleting && <p>Wait ka lang tol, Nag dedelete pa...</p>}
              </DialogContent>
              <DialogActions className={classes.modalFooter}>
                {isUploading ? (
                  <h4>Wait lang nag a upload pa</h4>
                ) : (
                  willDo &&
                  willDo == "add" && (
                    <Button onClick={() => handleSubmit()} color="transparent">
                      Submit
                    </Button>
                  )
                )}
                <Button
                  onClick={() => handleClose("classicModal")}
                  color="danger"
                  simple
                >
                  Close
                </Button>
              </DialogActions>
            </Dialog>
          </GridItem>
        </GridContainer>
      </div>
    );
  } else {
    return <h4>Loading, please wait...</h4>;
  }
}

export default withStyles(notificationsStyle)(ProductImage);
