import React, { useState, useEffect } from "react";

// @material-ui/core components
import withStyles from "@material-ui/core/styles/withStyles";
import FormLabel from "@material-ui/core/FormLabel";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import { withRouter } from "react-router-dom";

import Checkbox from "@material-ui/core/Checkbox";

// @material-ui/icons
import Check from "@material-ui/icons/Check";
// core components
import GridContainer from "../../components/Grid/GridContainer.jsx";
import GridItem from "../../components/Grid/GridItem.jsx";
import CustomInput from "../../components/CustomInput/CustomInput.jsx";
import Card from "../../components/Card/Card.jsx";
import CardHeader from "../../components/Card/CardHeader.jsx";
import CardText from "../../components/Card/CardText.jsx";
import CardBody from "../../components/Card/CardBody.jsx";

import regularFormsStyle from "../../assets/jss/material-dashboard-pro-react/views/regularFormsStyle";
import { MenuItem, FormControl, InputLabel, Select } from "@material-ui/core";
import Button from "../../components/CustomButtons/Button.jsx";
import TagsInput from "react-tagsinput";
import "./Product.css";
import axios from "axios";
import { toast } from "react-toastify";

let host = process.env.REACT_APP_API_URL;

function EditProduct(props) {
  const [product, setProduct] = useState({
    name: "",
    description: "",
    regularPrice: "",
    discountPrice: "",
    type: "",
    slug: "",
    tags: [],
    categories: [],
  });

  const [catProduct, setcatProduct] = useState([]);
  const [subCatProduct, setsubCatProduct] = useState([]);
  const [categories, setcategories] = useState(null);
  const [isLoading, setisLoading] = useState(false);

  const handleCategory = (id) => {
    let index = catProduct.findIndex((item) => item.id === id);

    if (index === -1) {
      catProduct.push({ id: id });
    } else {
      catProduct.splice(index, 1);
    }

    setcatProduct([...catProduct]);
  };

  const handleSubCategory = (id) => {
    let index = subCatProduct.findIndex((item) => item.id === id);

    if (index === -1) {
      subCatProduct.push({ id: id });
    } else {
      subCatProduct.splice(index, 1);
    }

    setsubCatProduct([...subCatProduct]);
    console.log(subCatProduct);
  };

  const handleChange = (e) => {
    setProduct({ ...product, [e.target.name]: e.target.value });
  };

  const handleTags = (tags) => {
    setProduct({ ...product, tags: tags });
  };

  const isValid = () => {
    if (
      !product.name ||
      !product.description ||
      !product.regularPrice ||
      !product.discountPrice ||
      !product.type ||
      !product.discountPrice ||
      !product.slug ||
      !product.tags
    ) {
      toast.error("Please input all fields");
      return false;
    }

    return true;
  };

  const fetchCategories = async () => {
    try {
      let res = await axios.get(`${host}/api/user/category`);

      if (res) {
        setcategories(res.data.categories);
        for (let i = 0; i < product.categories.length; i++) {
          let el = product.categories[i];
          catProduct.push({ id: el.id });
        }
        for (let j = 0; j < product.sub_categories.length; j++) {
          let el = product.sub_categories[j];
          subCatProduct.push({ id: el.id });
        }
        setcatProduct([...catProduct]);
        setsubCatProduct([...setsubCatProduct]);
      }
    } catch (error) {
      //   console.log(error);
    }
  };

  const fetchProducts = async () => {
    try {
      let res = await axios.get(`${host}/api/admin/product/by?id=${props.id}`);

      if (res) {
        setProduct({
          id: res.data.product.id,
          name: res.data.product.name,
          description: res.data.product.description,
          regularPrice: res.data.product.regular_price,
          discountPrice: res.data.product.discount_price,
          type: res.data.product.type,
          slug: res.data.product.slug,
          tags: res.data.product.tags.split(","),
          categories: res.data.product.categories,
          sub_categories: res.data.product.sub_categories,
        });
      }
    } catch (error) {
      //   console.log(error);
    }
  };

  const handleSubmit = async () => {
    if (isValid()) {
      setisLoading(true);
      try {
        let res = await axios.patch(`${host}/api/admin/product/${product.id}`, {
          // product data
          name: product.name,
          slug: product.slug,
          type: product.type,
          description: product.description,
          tags: product.tags.join(),
          regular_price: parseInt(product.regularPrice),
          discount_price: parseInt(product.discountPrice),
          // pivot ids
          categories: catProduct,
          sub_categories: subCatProduct,
        });
        if (res) {
          toast.success("Product updated succesfully.");
          props.history.push("/admin/products");
          setisLoading(false);
        }
      } catch (error) {
        setisLoading(false);
        //   console.log(error);
      }
    }
  };

  useEffect(() => {
    if (product.categories) {
      fetchCategories();
    }
  }, [product]);

  useEffect(() => {
    fetchProducts();
  }, []);

  const { classes } = props;

  if (product.id && categories) {
    return (
      <GridContainer>
        <GridItem xs={12} sm={12} md={12}>
          <Card>
            <CardHeader color="rose" text>
              <CardText color="rose">
                <h4 className={classes.cardTitle}>Edit Product</h4>
              </CardText>
            </CardHeader>
            <CardBody>
              <form>
                <GridContainer>
                  <GridItem xs={12} sm={2}>
                    <FormLabel className={classes.labelHorizontal}>
                      Product Title
                    </FormLabel>
                  </GridItem>
                  <GridItem xs={12} sm={10}>
                    <CustomInput
                      id="title"
                      formControlProps={{
                        fullWidth: true,
                      }}
                      inputProps={{
                        placeholder: "Product Title",
                      }}
                      value={product.name}
                      name="name"
                      onChange={handleChange}
                    />
                  </GridItem>
                </GridContainer>

                <GridContainer>
                  <GridItem xs={12} sm={2}>
                    <FormLabel className={classes.labelHorizontal}>
                      Product Description
                    </FormLabel>
                  </GridItem>
                  <GridItem xs={12} sm={10}>
                    <CustomInput
                      id="title"
                      formControlProps={{
                        fullWidth: true,
                      }}
                      inputProps={{
                        placeholder: "Product Description",
                      }}
                      value={product.description}
                      name="description"
                      onChange={handleChange}
                    />
                  </GridItem>
                </GridContainer>

                <GridContainer>
                  <GridItem xs={12} sm={2}>
                    <FormLabel
                      className={
                        classes.labelHorizontal +
                        " " +
                        classes.labelHorizontalRadioCheckbox
                      }
                    >
                      Categories
                    </FormLabel>
                  </GridItem>
                  <GridItem>
                    <GridContainer>
                      {categories &&
                        categories.map((cat, i) => {
                          return (
                            <GridItem xs={12} sm={10} md={4} lg={4} key={i}>
                              <div
                                className={
                                  classes.checkboxAndRadio +
                                  " " +
                                  classes.checkboxAndRadioHorizontal
                                }
                              >
                                <FormControlLabel
                                  control={
                                    <Checkbox
                                      tabIndex={-1}
                                      onChange={() => handleCategory(cat.id)}
                                      checkedIcon={
                                        <Check
                                          className={classes.checkedIcon}
                                        />
                                      }
                                      icon={
                                        <Check
                                          className={classes.uncheckedIcon}
                                        />
                                      }
                                      classes={{
                                        checked: classes.checked,
                                        root: classes.checkRoot,
                                      }}
                                      checked={
                                        catProduct.some((e) => e.id == cat.id)
                                          ? true
                                          : false
                                      }
                                    />
                                  }
                                  classes={{
                                    label: classes.categoryLabel,
                                  }}
                                  label={cat.name}
                                />
                              </div>
                              {cat.sub_categories.length
                                ? cat.sub_categories.map((subCat, i) => (
                                    <div
                                      key={i}
                                      className={
                                        classes.checkboxAndRadio +
                                        " " +
                                        classes.checkboxAndRadioHorizontal
                                      }
                                    >
                                      <FormControlLabel
                                        control={
                                          <Checkbox
                                            tabIndex={-1}
                                            onClick={() =>
                                              handleSubCategory(subCat.id)
                                            }
                                            checkedIcon={
                                              <Check
                                                className={classes.checkedIcon}
                                              />
                                            }
                                            icon={
                                              <Check
                                                className={
                                                  classes.uncheckedIcon
                                                }
                                              />
                                            }
                                            classes={{
                                              checked: classes.checked,
                                              root: classes.checkRoot,
                                            }}
                                            checked={
                                              subCatProduct.some(
                                                (e) => e.id == subCat.id
                                              )
                                                ? true
                                                : false
                                            }
                                          />
                                        }
                                        classes={{
                                          label: classes.label,
                                        }}
                                        label={subCat.name}
                                      />
                                    </div>
                                  ))
                                : null}
                              <div
                                className={
                                  classes.checkboxAndRadio +
                                  " " +
                                  classes.checkboxAndRadioHorizontal
                                }
                              />
                              <div
                                className={
                                  classes.checkboxAndRadio +
                                  " " +
                                  classes.checkboxAndRadioHorizontal
                                }
                              />
                            </GridItem>
                          );
                        })}
                    </GridContainer>
                  </GridItem>
                  <br />

                  <GridContainer>
                    <GridItem xs={12} sm={2}>
                      <FormLabel className={classes.labelHorizontal}>
                        Regular Price
                      </FormLabel>
                    </GridItem>
                    <GridItem xs={12} sm={10}>
                      <CustomInput
                        id="title"
                        formControlProps={{
                          fullWidth: true,
                        }}
                        inputProps={{
                          placeholder: "Regular Price",
                        }}
                        value={product.regularPrice}
                        name="regularPrice"
                        onChange={handleChange}
                      />
                    </GridItem>
                  </GridContainer>

                  <GridContainer>
                    <GridItem xs={12} sm={2}>
                      <FormLabel className={classes.labelHorizontal}>
                        Discounted Price
                      </FormLabel>
                    </GridItem>
                    <GridItem xs={12} sm={10}>
                      <CustomInput
                        id="title"
                        formControlProps={{
                          fullWidth: true,
                        }}
                        inputProps={{
                          placeholder: "Discounted Price",
                        }}
                        value={product.discountPrice}
                        name="discountPrice"
                        onChange={handleChange}
                      />
                    </GridItem>
                  </GridContainer>

                  <GridContainer>
                    <GridItem xs={12} sm={2}>
                      <FormLabel
                        className={
                          classes.labelHorizontal +
                          " " +
                          classes.labelHorizontalRadioCheckbox
                        }
                      >
                        Type
                      </FormLabel>
                    </GridItem>
                    <GridItem xs={12} sm={6} md={5} lg={5}>
                      <FormControl
                        fullWidth
                        className={classes.selectFormControl}
                      >
                        <InputLabel
                          htmlFor="simple-select"
                          className={classes.selectLabel}
                        >
                          Choose Type
                        </InputLabel>
                        <Select
                          MenuProps={{
                            className: classes.selectMenu,
                          }}
                          classes={{
                            select: classes.select,
                          }}
                          value={product.type}
                          onChange={handleChange}
                          inputProps={{
                            name: "type",
                            id: "simple-select",
                          }}
                        >
                          <MenuItem
                            disabled
                            classes={{
                              root: classes.selectMenuItem,
                            }}
                          >
                            Choose City
                          </MenuItem>
                          <MenuItem
                            classes={{
                              root: classes.selectMenuItem,
                              selected: classes.selectMenuItemSelected,
                            }}
                            value="main"
                          >
                            Main
                          </MenuItem>
                          <MenuItem
                            classes={{
                              root: classes.selectMenuItem,
                              selected: classes.selectMenuItemSelected,
                            }}
                            value="addon"
                          >
                            Addon
                          </MenuItem>
                        </Select>
                      </FormControl>
                    </GridItem>
                  </GridContainer>
                  <GridContainer>
                    <GridItem xs={12} sm={2}>
                      <FormLabel className={classes.labelHorizontal}>
                        Product Slug
                      </FormLabel>
                    </GridItem>
                    <GridItem xs={12} sm={10}>
                      <CustomInput
                        id="title"
                        formControlProps={{
                          fullWidth: true,
                        }}
                        inputProps={{
                          placeholder: "Product Slug",
                        }}
                        value={product.slug}
                        name="slug"
                        onChange={handleChange}
                      />
                    </GridItem>
                  </GridContainer>
                  <GridContainer>
                    <GridItem xs={12} sm={2}>
                      {/* <FormLabel className={classes.labelHorizontal}>
                          Product Tag
                        </FormLabel> */}
                    </GridItem>
                    <GridItem xs={12} sm={12} md={6}>
                      <TagsInput
                        value={product.tags}
                        onChange={handleTags}
                        tagProps={{ className: "react-tagsinput-tag info" }}
                      />
                    </GridItem>
                  </GridContainer>
                </GridContainer>
                <div style={{ float: "right" }}>
                  <Button
                    color="danger"
                    className={classes.marginRight}
                    onClick={() => (props.history.push("/admin/products"))}
                  >
                    Cancel
                  </Button>
                  <Button
                    color="success"
                    className={classes.marginRight}
                    onClick={handleSubmit}
                  >
                    {isLoading ? (
                      <>
                        <div class="lds-ring">
                          <div />
                          <div />
                          <div />
                          <div />
                        </div>{" "}
                      </>
                    ) : (
                      "Submit"
                    )}
                  </Button>
                </div>
              </form>
            </CardBody>
          </Card>
        </GridItem>
      </GridContainer>
    );
  } else {
    return <h1>Loading, please wait q</h1>;
  }
}

export default withRouter(withStyles(regularFormsStyle)(EditProduct));
