import React, { useState, useEffect } from "react";
import { withRouter } from "react-router-dom";

// @material-ui/core components
import withStyles from "@material-ui/core/styles/withStyles";
import FormLabel from "@material-ui/core/FormLabel";
import FormControlLabel from "@material-ui/core/FormControlLabel";

import Checkbox from "@material-ui/core/Checkbox";

// @material-ui/icons
import Check from "@material-ui/icons/Check";
// core components
import GridContainer from "../../components/Grid/GridContainer.jsx";
import GridItem from "../../components/Grid/GridItem.jsx";
import CustomInput from "../../components/CustomInput/CustomInput.jsx";
import Card from "../../components/Card/Card.jsx";
import CardHeader from "../../components/Card/CardHeader.jsx";
import CardText from "../../components/Card/CardText.jsx";
import CardBody from "../../components/Card/CardBody.jsx";

import regularFormsStyle from "../../assets/jss/material-dashboard-pro-react/views/regularFormsStyle";
import { MenuItem, FormControl, InputLabel, Select } from "@material-ui/core";
import Button from "../../components/CustomButtons/Button.jsx";
import TagsInput from "react-tagsinput";
import "./EventSettings.css";
import axios from "axios";
import { toast } from "react-toastify";

let host = process.env.REACT_APP_API_URL;

function EventSettings(props) {
  const [event, setEvent] = useState({
    title: "",
    location: "",
    event_date: "",
  });
  const [categories, setcategories] = useState(null);
  const [isLoading, setisLoading] = useState(false);
  const handleChange = (e) => {
    setEvent({ ...event, [e.target.name]: e.target.value });
  };

  const isValid = () => {
    if (!event.title || !event.location || !event.event_date) {
      toast.error("Please input all fields");
      return false;
    }

    return true;
  };

  const fetchEvent = async () => {
    try {
      let res = await axios.get(`${host}/api/admin/dashboard/event`);

      if (res) {
        const { title, location, event_date, products } =
          res.data.event.rows[0];
        setEvent({
          title,
          location,
          event_date,
        });
      }
    } catch (error) {
      //   console.log(error);
    }
  };

  const handleSubmit = async () => {
    if (isValid()) {
    //   setisLoading(true);
      try {
        let res = await axios.post(`${host}/api/admin/product`, {
          // product data
        });
        if (res) {
          props.history.push("/admin/products");
          setisLoading(false);
        }
      } catch (error) {
        setisLoading(false);
        //   console.log(error);
      }
    }
  };

  useEffect(() => {
    fetchEvent();
  }, []);

  const { classes } = props;

  if (!isLoading) {
    return (
      <GridContainer>
        <GridItem xs={12} sm={12} md={12}>
          <Card>
            <CardHeader color="rose" text>
              <CardText color="rose">
                <h4 className={classes.cardTitle}>Event Settings</h4>
              </CardText>
            </CardHeader>
            <CardBody>
              <form>
                <GridContainer>
                  <GridItem xs={12} sm={2}>
                    <FormLabel className={classes.labelHorizontal}>
                      Event Title
                    </FormLabel>
                  </GridItem>
                  <GridItem xs={12} sm={10}>
                    <CustomInput
                      id="title"
                      formControlProps={{
                        fullWidth: true,
                      }}
                      inputProps={{
                        placeholder: "Event Title",
                      }}
                      value={event.title}
                      name="title"
                      onChange={handleChange}
                    />
                  </GridItem>
                </GridContainer>

                <GridContainer>
                  <GridItem xs={12} sm={2}>
                    <FormLabel className={classes.labelHorizontal}>
                      Event Location
                    </FormLabel>
                  </GridItem>
                  <GridItem xs={12} sm={10}>
                    <CustomInput
                      id="title"
                      formControlProps={{
                        fullWidth: true,
                      }}
                      inputProps={{
                        placeholder: "Event Location",
                      }}
                      value={event.location}
                      name="location"
                      onChange={handleChange}
                    />
                  </GridItem>
                </GridContainer>

                <GridContainer>
                  <GridItem xs={12} sm={2}>
                    <FormLabel className={classes.labelHorizontal}>
                      Event Date
                    </FormLabel>
                  </GridItem>
                  <GridItem xs={12} sm={10}>
                    <CustomInput
                      id="title"
                      formControlProps={{
                        fullWidth: true,
                      }}
                      inputProps={{
                        placeholder: "Event Date",
                        type: "date",
                      }}
                      value={event.event_date}
                      name="event_date"
                      onChange={handleChange}
                    />
                  </GridItem>
                </GridContainer>

                <div style={{ float: "right" }}>
                  <Button
                    color="danger"
                    className={classes.marginRight}
                    onClick={() => props.history.push("/admin/products")}
                  >
                    Cancel
                  </Button>
                  <Button
                    color="success"
                    className={classes.marginRight}
                    onClick={handleSubmit}
                  >
                    {isLoading ? (
                      <>
                        <div class="lds-ring">
                          <div />
                          <div />
                          <div />
                          <div />
                        </div>{" "}
                      </>
                    ) : (
                      "Submit"
                    )}
                  </Button>
                </div>
              </form>
            </CardBody>
          </Card>
        </GridItem>
      </GridContainer>
    );
  } else {
    return <h1>Loading, please wait</h1>;
  }
}

export default withRouter(withStyles(regularFormsStyle)(EventSettings));
