import React, { useState, useEffect } from "react";

// @material-ui/core ../../components
import withStyles from "@material-ui/core/styles/withStyles";
import FormLabel from "@material-ui/core/FormLabel";

// core ../../components
import GridContainer from "../../components/Grid/GridContainer.jsx";
import GridItem from "../../components/Grid/GridItem.jsx";
import CustomInput from "../../components/CustomInput/CustomInput.jsx";
import Card from "../../components/Card/Card.jsx";
import CardHeader from "../../components/Card/CardHeader.jsx";
import CardText from "../../components/Card/CardText.jsx";
import CardBody from "../../components/Card/CardBody.jsx";

import regularFormsStyle from "../../assets/jss/material-dashboard-pro-react/views/regularFormsStyle";
import Button from "../../components/CustomButtons/Button.jsx";
// import "./category.css";
import axios from "axios";
import { toast } from "react-toastify";

let host = process.env.REACT_APP_API_URL;

function EditCategory(props) {
  const [category, setcategory] = useState({
    name: props.category.name,
    slug: props.category.slug,
  });

  const [isLoading, setisLoading] = useState(false);

  const handleChange = (e) => {
    setcategory({ ...category, [e.target.name]: e.target.value });
  };

  const isValid = () => {
    if (!category.name || !category.slug) {
      toast.error("Please input all fields");
      return false;
    }

    return true;
  };

  const handleSubmit = async () => {
    if (isValid()) {
      setisLoading(true);
      try {
        let res = await axios.patch(
          `${host}/api/admin/category/${props.category.id}`,
          {
            // category data
            name: category.name,
            slug: category.slug,
          }
        );
        if (res) {
          // window.location.href = "/admin/category";
          toast.success("Category updated succesfully.");
          setisLoading(false);
        }
      } catch (error) {
        setisLoading(false);
        //   console.log(error);
      }
    }
  };

  useEffect(() => {
    setcategory({
      name: props.category.name,
      slug: props.category.slug,
    });
  }, [props.category]);

  const { classes } = props;

  return (
    <GridContainer style={{ minWidth: "1000px" }}>
      <GridItem xs={12} sm={12} md={12}>
        <Card>
          <CardHeader color="rose" text>
            <CardText color="rose">
              <h6 className={classes.cardTitle}>Edit Category</h6>
            </CardText>
          </CardHeader>
          <CardBody>
            <form>
              <GridContainer>
                <GridItem xs={12} sm={2}>
                  <FormLabel className={classes.labelHorizontal}>
                    Product Name
                  </FormLabel>
                </GridItem>
                <GridItem xs={12} sm={10}>
                  <CustomInput
                    id="title"
                    formControlProps={{
                      fullWidth: true,
                    }}
                    inputProps={{
                      placeholder: "Category Name",
                    }}
                    value={category.name}
                    name="name"
                    onChange={handleChange}
                  />
                </GridItem>
              </GridContainer>

              <GridContainer>
                <GridItem xs={12} sm={2}>
                  <FormLabel className={classes.labelHorizontal}>
                    Product Slug
                  </FormLabel>
                </GridItem>
                <GridItem xs={12} sm={10}>
                  <CustomInput
                    id="title"
                    formControlProps={{
                      fullWidth: true,
                    }}
                    inputProps={{
                      placeholder: "Category Slug",
                    }}
                    value={category.slug}
                    name="slug"
                    onChange={handleChange}
                  />
                </GridItem>
              </GridContainer>

              <div style={{ float: "right" }}>
                <Button
                  color="success"
                  className={classes.marginRight}
                  onClick={handleSubmit}
                >
                  {isLoading ? (
                    <>
                      <div class="lds-ring">
                        <div />
                        <div />
                        <div />
                        <div />
                      </div>{" "}
                    </>
                  ) : (
                    "Submit"
                  )}
                </Button>
              </div>
            </form>
          </CardBody>
        </Card>
      </GridItem>
    </GridContainer>
  );
}

export default withStyles(regularFormsStyle)(EditCategory);
