import React, { useState, useEffect } from "react";

// @material-ui/core components
import withStyles from "@material-ui/core/styles/withStyles";
import FormLabel from "@material-ui/core/FormLabel";

// core components
import GridContainer from "../../components/Grid/GridContainer.jsx";
import GridItem from "../../components/Grid/GridItem.jsx";
import CustomInput from "../../components/CustomInput/CustomInput.jsx";
import Card from "../../components/Card/Card.jsx";
import CardHeader from "../../components/Card/CardHeader.jsx";
import CardText from "../../components/Card/CardText.jsx";
import CardBody from "../../components/Card/CardBody.jsx";

import regularFormsStyle from "../../assets/jss/material-dashboard-pro-react/views/regularFormsStyle";
import Button from "../../components/CustomButtons/Button.jsx";
// import "./category.css";
import axios from "axios";
import {
  FormControl,
  RadioGroup,
  FormControlLabel,
  Radio,
} from "@material-ui/core";
import { toast } from "react-toastify";

let host = process.env.REACT_APP_API_URL;

function EditSubCategory(props) {
  const [subcategory, setsubcategory] = useState({
    id: props.subcategory.id,
    name: props.subcategory.name,
    slug: props.subcategory.slug,
    category_id: props.subcategory.category_id,
  });

  const [categories, setcategories] = useState(null);
  const [isLoading, setisLoading] = useState(false);

  const handleChange = (e) => {
    setsubcategory({ ...subcategory, [e.target.name]: e.target.value });
  };

  const fetchCategories = async () => {
    try {
      let res = await axios.get(`${host}/api/admin/category`);

      if (res) {
        setcategories(res.data.categories);
      }
    } catch (error) {
      //   console.log(error);
    }
  };

  const isValid = () => {
    if (!subcategory.name || !subcategory.slug) {
      toast.error("Please input all fields");
      return false;
    }

    return true;
  };

  const handleSubmit = async () => {
    if (isValid()) {
      setisLoading(true);
      try {
        let res = await axios.patch(
          `${host}/api/admin/sub_category/${props.subcategory.id}`,
          {
            // subcategory data
            name: subcategory.name,
            slug: subcategory.slug,
            category_id: subcategory.category_id,
          }
        );
        if (res) {
          // window.location.href = "/admin/subcategory";
          toast.success("subcategory updated succesfully.");
          setisLoading(false);
        }
      } catch (error) {
        setisLoading(false);
        //   console.log(error);
      }
    }
  };

  useEffect(() => {
    setsubcategory({
      id: props.subcategory.id,
      name: props.subcategory.name,
      slug: props.subcategory.slug,
      category_id: props.subcategory.category_id,
    });
  }, [props.subcategory]);

  useEffect(() => {
    fetchCategories();
  }, []);

  const { classes } = props;

  return (
    <GridContainer style={{ minWidth: "1000px" }}>
      <GridItem xs={12} sm={12} md={12}>
        <Card>
          <CardHeader color="rose" text>
            <CardText color="rose">
              <h6 className={classes.cardTitle}>Edit subcategory</h6>
            </CardText>
          </CardHeader>
          <CardBody>
            <form>
              <GridContainer>
                <GridItem xs={12} sm={2}>
                  <FormLabel className={classes.labelHorizontal}>
                    Categories
                  </FormLabel>
                </GridItem>
                <GridItem xs={12} sm={2}>
                  <FormControl component="fieldset">
                    <RadioGroup
                      aria-label="gender"
                      name="category_id"
                      // value={value}
                      onChange={handleChange}
                    >
                      {categories &&
                        categories.map((cat, i) => (
                          <FormControlLabel
                            value={cat.id.toString()}
                            control={
                              <Radio
                                checked={cat.id == subcategory.category_id}
                              />
                            }
                            label={cat.name.toString()}
                          />
                        ))}
                    </RadioGroup>
                  </FormControl>
                </GridItem>
              </GridContainer>
              <GridContainer>
                <GridItem xs={12} sm={2}>
                  <FormLabel className={classes.labelHorizontal}>
                    Product Name
                  </FormLabel>
                </GridItem>
                <GridItem xs={12} sm={10}>
                  <CustomInput
                    id="title"
                    formControlProps={{
                      fullWidth: true,
                    }}
                    inputProps={{
                      placeholder: "subcategory Name",
                    }}
                    value={subcategory.name}
                    name="name"
                    onChange={handleChange}
                  />
                </GridItem>
              </GridContainer>

              <GridContainer>
                <GridItem xs={12} sm={2}>
                  <FormLabel className={classes.labelHorizontal}>
                    Product Slug
                  </FormLabel>
                </GridItem>
                <GridItem xs={12} sm={10}>
                  <CustomInput
                    id="title"
                    formControlProps={{
                      fullWidth: true,
                    }}
                    inputProps={{
                      placeholder: "subcategory Slug",
                    }}
                    value={subcategory.slug}
                    name="slug"
                    onChange={handleChange}
                  />
                </GridItem>
              </GridContainer>

              <div style={{ float: "right" }}>
                <Button
                  color="success"
                  className={classes.marginRight}
                  onClick={handleSubmit}
                >
                  {isLoading ? (
                    <>
                      <div class="lds-ring">
                        <div />
                        <div />
                        <div />
                        <div />
                      </div>{" "}
                    </>
                  ) : (
                    "Submit"
                  )}
                </Button>
              </div>
            </form>
          </CardBody>
        </Card>
      </GridItem>
    </GridContainer>
  );
}

export default withStyles(regularFormsStyle)(EditSubCategory);
