import React, { useState, useEffect } from "react";
import MultiSelect from "react-multi-select-component";

// @material-ui/core components
import withStyles from "@material-ui/core/styles/withStyles";

// core components
import GridContainer from "../../components/Grid/GridContainer.jsx";
import GridItem from "../../components/Grid/GridItem.jsx";
import Card from "../../components/Card/Card.jsx";
import CardBody from "../../components/Card/CardBody.jsx";

import regularFormsStyle from "../../assets/jss/material-dashboard-pro-react/views/regularFormsStyle";
import Button from "../../components/CustomButtons/Button.jsx";
// import "./Product.css";
import axios from "axios";
import CreateCategory from "../Category/CreateCategory";
import { toast } from "react-toastify";

let host = process.env.REACT_APP_API_URL;

function CollectionProduct(props) {
  const [products, setProducts] = useState(props.products);
  const [collection, setCollection] = useState(null);
  const [options, setoptions] = useState([]);

  const [selected, setSelected] = useState([]);
  const [isLoading, setisLoading] = useState(false);

  const isValid = () => {
    // if (
    //   !product.name ||
    //   !product.description ||
    //   !product.regularPrice ||
    //   !product.discountPrice ||
    //   !product.type ||
    //   !product.discountPrice ||
    //   !product.slug ||
    //   !product.tags
    // ) {
    //   alert("Please input all fields");
    //   return false;
    // }
    return true;
  };

  const handleSubmit = async () => {
    if (isValid()) {
      setisLoading(true);
      try {
        let res = await axios.patch(
          `${host}/api/admin/collection/product/${collection.id}`,
          selected.map((a) => a.value)
        );
        if (res) {
          toast.success("Collection Products updated succesfully.");
          // props.handleClose("classicModal");
          setisLoading(false);
        }
      } catch (error) {
        setisLoading(false);
        //   console.log(error);
      }
    }
  };

  useEffect(() => {
    if (props.products && props.collection) {
      setProducts(props.collection.products);
      let newOption = [];
      let newSelected = [];
      for (let i = 0; i < props.products.length; i++) {
        const el = props.products[i];
        if (props.collection.products.some((item) => item.id == el.id)) {
          newSelected.push({ label: el.name, value: el.id });
        }
        newOption.push({ label: el.name, value: el.id });
      }
      setSelected(newSelected);
      setoptions(newOption);
    }
  }, [props.products, props.collection]);

  useEffect(() => {
    setCollection(props.collection);
    console.log(props.collection);
  }, [props.collection]);

  const { classes } = props;

  if (products && collection) {
    return (
      <GridContainer style={{ minWidth: "1000px" }}>
        <GridItem xs={12} sm={12} md={12}>
          <Card>
            <CardBody>
              {options.length ? (
                <div>
                  <p>Select Products</p>
                  <MultiSelect
                    options={options}
                    value={selected}
                    onChange={setSelected}
                    labelledBy={"Select"}
                  />
                </div>
              ) : (
                <h5>Loading, please wait...</h5>
              )}

              <div style={{ visibility: "hidden" }}>
                <CreateCategory />
              </div>
              <div style={{ float: "right" }}>
                <Button
                  color="success"
                  className={classes.marginRight}
                  onClick={handleSubmit}
                >
                  {isLoading ? (
                    <>
                      <div class="lds-ring">
                        <div />
                        <div />
                        <div />
                        <div />
                      </div>{" "}
                    </>
                  ) : (
                    "Submit"
                  )}
                </Button>
              </div>
            </CardBody>
          </Card>
        </GridItem>
      </GridContainer>
    );
  } else {
    return <h1>Loading, please wait</h1>;
  }
}

export default withStyles(regularFormsStyle)(CollectionProduct);
